import { LocalFileService } from "../../services/LocalFileService";
import { PXFile } from "./PXFile";
import { LocalFileOpenMode } from "../../defs/LocalFileOpenMode";

export class PXFileManager {
    private files: Map<string, PXFile> = new Map();

    constructor(public localFileService: LocalFileService) {
    }

    /**
     * 打开文件
     * @param path
     */
    public open(path: string, mode: LocalFileOpenMode): Promise<PXFile> {
        // todo: 将path转化为绝对路径
        let file: PXFile = null;
        if (!this.files.has(path)) { // 不存在则创建
            file = new PXFile(path, mode, this);
            this.files.set(path, file);
        } else {
            file = this.files.get(path);
        }
        // console.log("打开文件");
        return file.open(mode).then((e) => {
            return Promise.resolve(file);
        });
    }

    /**
     * 关闭所有文件
     */
    public release() {
        this.files.forEach((afile) => {
            afile.close();
        });
    }
}
