export class Vector2 implements IVector2 {
    constructor(public x: number, public y: number) {
    }

    public static toArray(vectors: IVector2[]): number[] {
        const arr: number[] = [];

        vectors.forEach((vec) => {
            arr.push(vec.x);
            arr.push(vec.y);
        });
        return arr;
    }

    public static fromArray(arr: number[]): Vector2[] {
        const vectors: Vector2[] = [];
        let p = 0;
        const count = arr.length / 2;
        for (let i = 0; i < count; ++i) {
            vectors.push(new Vector2(arr[p++], arr[p++]));
        }
        return vectors;
    }
}
