export class MessageConsumer implements IMessageConsumer {
    /**
     * 消息列表
     */
    private messageMap: Map<string, any[]>;

    constructor() {
        this.messageMap = new Map();
    }

    public execute(msg: string) {
        if (msg === "") {
          console.error("msg非法");
          return;
        }
        // console.warn(msg);
        const msgJsonObject = JSON.parse(msg) as IMessageData;
        const cmd = msgJsonObject.cmd;
        const data = msgJsonObject.data;
        const callbacks: Array<(...args: any[]) => void> = this.messageMap.get(cmd);
        if (callbacks === undefined) {
            console.warn("未注册指令 " + cmd);
            return;
        }
        for (const callback of callbacks) {
            callback(data);
        }
    }

    /**
     * 注册消息回调
     * @param cmd 消息名称
     * @param callback 回调
     */
    public registerCallback(cmd: string, callback: (...args: any[]) => void, context: any) {
        if (!this.messageMap.has(cmd)) {
            this.messageMap.set(cmd, []);
        }
        this.messageMap.get(cmd).push(callback.bind(context));
    }

    /**
     * 注销消息回调
     * @param cmd 消息名称
     * @param callback 回调
     */
    public unregisterCallback(cmd: string, callback: (...args: any[]) => void) {
        if (!this.messageMap.has(cmd)) {
            return;
        }

        const callbackList = this.messageMap.get(cmd);
        const index = callbackList.indexOf(callback);
        if (index >= 0 ) {
            callbackList.splice(index, 1);
        }
    }
}
