export class Log {
    private static consoleLabel: HTMLLabelElement = null;
    public static log(...args: any[]): void {
        console.log(...args);
        this.setLabel(...args);
    }

    public static warn(...args: any[]): void {
        console.warn(...args);
        this.setLabel(...args);
    }

    public static error(...args: any[]): void {
        console.error(...args);
        this.setLabel(...args);
    }

    private static hasLabel: boolean = true;
    private static setLabel(...args: any[]): boolean {
        if (this.hasLabel === false) {
            return false;
        }
        if (this.consoleLabel === null) {
            this.consoleLabel = document.getElementById("consoleLabel") as HTMLLabelElement;
        }
        if (this.consoleLabel === null || this.consoleLabel === undefined) {
            this.hasLabel = false;
            return false;
        }
        try {
            this.consoleLabel.innerText += "\n" + JSON.stringify(args);
        } catch (reason) {
            // console.log(args);
        }
        return true;
    }
}
