interface IAIBinding {
    stuId: string;
    gid: string;
}
export class StudentGalleryBinding {
    private bindings: IAIBinding[] = [];

    /**
     * 根据gid获取绑定关系
     * @param gid
     */
    public getBindingByGid(gid: string): IAIBinding {
        for (let i = 0; i < this.bindings.length; ++i) {
            if (this.bindings[i].gid === gid) {
                return this.bindings[i];
            }
        }
    }

    /**
     * 根据stuid获取绑定关系
     * @param stuid
     */
    public getBindingByStuId(stuid: string): IAIBinding {
        for (let i = 0; i < this.bindings.length; ++i) {
            if (this.bindings[i].stuId === stuid) {
                return this.bindings[i];
            }
        }
    }

    /**
     * 删除gid对应的绑定关系
     * @param gid
     */
    public removeBindingByGid(gid: string): boolean {
        for (let i = this.bindings.length - 1; i >= 0; --i) {
            if (this.bindings[i].gid === gid) {
                this.bindings.splice(i, 1);
                return true;
            }
        }
        return false;
    }

    /**
     * 删除stuId对应的绑定关系
     * @param stuid
     */
    public removeBindingByStuId(stuid: string): boolean {
        for (let i = this.bindings.length - 1; i >= 0; --i) {
            if (this.bindings[i].stuId === stuid) {
                this.bindings.splice(i, 1);
                return true;
            }
        }
        return false;
    }

    /**
     * 添加binding 该方法会先删除之前stuid或gid重复的binding
     * @param binding 新的绑定关系
     */
    public addBinding(binding: IAIBinding): void {
        this.removeBindingByGid(binding.gid);
        this.removeBindingByStuId(binding.stuId);
        this.bindings.push(binding);
    }

    /**
     * 根据stuid获取gid
     * 若不存在则返回null
     * @param stuId
     */
    public getGid(stuId: string): string {
        const binding = this.getBindingByStuId(stuId);
        if (binding !== null) {
            return binding.gid;
        }
        return null;
    }
}
