import { MessageConsumer } from "./MessageConsumer";
import { WindowMessageTransfer } from "./unity/WindowMessageTransfer";
import { UnityMessageTransfer } from "./unity/UnityMessageTransfer";
import { AIBrowser } from "./frame/AIBrowser";
import { Mocker } from "./Mocker";
import { NativeBridge } from "./frame/NativeBridge";
import { ExtendedBridge } from "./ExtendedBridge";

declare global {
    // tslint:disable-next-line: interface-name
    interface Window {
        messageConsumer: IMessageConsumer;
        messager: WindowMessageTransfer;
        messageTransfer: UnityMessageTransfer;
        gameInstance: IGameInstance;
        aibrowser: IAIBrowser;
        bridge: NativeBridge;
        mocker: Mocker;
        qt: IQtObject;
        webChannel: QWebChannel;
        createNativeBridge: (callback: (bridge: NativeBridge) => void, serviceList: string[]) => void;
        getServiceList: () => string[];
    }
}

window.createNativeBridge = (callback: (bridge: NativeBridge) => void, serviceList: string[]) => {
    if (window.bridge !== undefined && window.bridge instanceof NativeBridge) { // 保证只初始化一次
        callback(window.bridge);
        return;
    }
    window.messageConsumer = new MessageConsumer();
    window.messager = new WindowMessageTransfer(false);
    window.gameInstance = null;
    const aibrowser = new AIBrowser();
    window.bridge = new ExtendedBridge(aibrowser);
    window.mocker = new Mocker(aibrowser, window.bridge);
    window.bridge.init().then(() => {
        return window.bridge.start(serviceList);
    }).then(() => {
        if (typeof callback === "function") {
            callback(window.bridge);
        }
    }).catch((reason) => {
        console.error(reason);
    });
};
