import { AnswerToolService } from "./services/AnswerToolService";
import { UnityService } from "./services/UnityService";
import { NativeBridge } from "./frame/NativeBridge";

export class Mocker {
    private aibrowser: IAIBrowser;
    private bridge: NativeBridge;
    public ids: string[] = [
        "100",
        "101",
        "102",
        "103",
        "104",
        "105",
        "106",
        "107",
        "108",
        "109",
    ];
    public names: string[] = [
        "赵小0",
        "赵小1",
        "赵小2",
        "赵小3",
        "赵小4",
        "赵小5",
        "赵小6",
        "赵小7",
        "赵小8",
        "赵小9",
    ];
    constructor(aibrowser: IAIBrowser, bridge: NativeBridge) {
        this.aibrowser = aibrowser;
        this.bridge = bridge;
    }

    public sendClassInfo(): void {
        const students: any[] = [];
        for (let i = 0; i < 10; ++i) {
            students.push({
                id: this.ids[i],
                name: this.names[i],
                gid: "",
            });
        }
        const unityService = this.bridge.getService(UnityService) as UnityService;
        unityService.transferToUnity("class_info", { students });
    }

    public getStudentsInfo(): Array<{stuID: string, stuName: string, gender: number, MRID: number}> {
        const students: Array<{stuID: string, stuName: string, gender: number, MRID: number}> = [];
        for (let i = 0; i < 10; ++i) {
            students.push({
                stuID: this.ids[i],
                stuName: this.names[i],
                gender: 0,
                MRID: -1,
            });
        }
        return students;

    }

    public bindAnswerTool(): void {
        const binds: any[] = [];
        for (let i = 0; i < 10; ++i) {
            binds.push({
                id: this.ids[i],
                row1: this.names[i],
                row2: this.names[i],
                row3: this.names[i],
            });
        }
        const data: any = {
            users: binds,
        };
        console.log(JSON.stringify(data));
        const answerToolService = this.bridge.getService(AnswerToolService) as AnswerToolService;
        answerToolService.px_cmd_answer_add_user(data);
    }

    public galleryResult = {
        galleries: [
            {
                faceImg: "data:image/jpg;base64,",
                gid: "",
                height: 111,
                showImg: "data:image/jpg;base64,",
                stuID: "",
                width: 87,
                x: 588,
                y: 602,
            },
            {
                faceImg: "data:image/jpg;base64,",
                gid: "",
                height: 88,
                showImg: "data:image/jpg;base64,",
                stuID: "",
                width: 73,
                x: 1431,
                y: 513,
            },
        ],
        image: "data:image/jpg;base64,",
    };
}
