import serviceDefs from "./ServiceMetaData";

export class CodeInterpreter {
    private exeMap: Map<number, string> = new Map();
    private infoMap: Map<number, string> = new Map();
    private msgMap: Map<number, string> = new Map();
    private errorMap: Map<number, string> = new Map();

    constructor() {
        serviceDefs.forEach((serviceDef) => {
            for (const item in serviceDef.messageCodeType) {
                if (isNaN(Number(item))) {
                    this.msgMap.set(serviceDef.messageCodeType[item], item);
                }
            }
            for (const item in serviceDef.executeCommandType) {
                if (isNaN(Number(item))) {
                    this.exeMap.set(serviceDef.executeCommandType[item], item);
                }
            }
            for (const item in serviceDef.infoCommandType) {
                if (isNaN(Number(item))) {
                    this.infoMap.set(serviceDef.infoCommandType[item], item);
                }
            }
            for (const item in serviceDef.errorCodeType) {
                if (isNaN(Number(item))) {
                    this.errorMap.set(serviceDef.errorCodeType[item], item);
                }
            }
        });
    }
    public getExeName(code: number): string {
        return this.exeMap.get(code);
    }

    public getInfoName(code: number): string {
        return this.infoMap.get(code);
    }

    public getMsgName(code: number): string {
        return this.msgMap.get(code);
    }
    public getErrorName(code: number): string {
        return this.errorMap.get(code);
    }
}
