export class Path {

    /**
     * 给定文件路径 返回文件名
     * @param filePath
     */
    public static getFileName(filePath: string, withoutExt: boolean = false): string {
        filePath = filePath.replace("\\", "/");
        filePath = filePath.replace("//", "/");
        let index = filePath.lastIndexOf("/");
        if (index < 0) {
            index = 0;
        } else {
            index += 1;
        }
        let fileName = filePath.substr(index);
        if (withoutExt) {
            // 去掉扩展后缀
            fileName = fileName.substr(0, fileName.indexOf("."));
        }
        return fileName;
    }

    /**
     * 给定文件路径 返回文件所在目录
     * @param filePath
     */
    public static getDirPath(filePath: string): string {
        filePath = filePath.replace("\\", "/");
        filePath = filePath.replace("//", "/");
        let index = filePath.lastIndexOf("/");
        if (index < 0) {
            index = 0;
        } else {
            index += 1;
        }
        const dirPath = filePath.substr(0, index);
        return dirPath;
    }
}
