export enum LocalFileOpenMode {
    PX_LOCALFILE_OPEN_FILE_MODE_NOT_OPEN = 0x0000,
    PX_LOCALFILE_OPEN_FILE_MODE_READ_ONLY = 0x0001,
    PX_LOCALFILE_OPEN_FILE_MODE_WRITE_ONLY = 0x0002,
    PX_LOCALFILE_OPEN_FILE_MODE_READ_WRITE = (PX_LOCALFILE_OPEN_FILE_MODE_READ_ONLY | PX_LOCALFILE_OPEN_FILE_MODE_WRITE_ONLY),
    PX_LOCALFILE_OPEN_FILE_MODE_APPEND = 0x0004,
    PX_LOCALFILE_OPEN_FILE_MODE_TRUNCATE = 0x0008,
    PX_LOCALFILE_OPEN_FILE_MODE_TEXT = 0x0010,
    PX_LOCALFILE_OPEN_FILE_MODE_UNBUFFERED = 0x0020,
}
