export class Flow {

    public static flow(promiseArray: Array<(...args: any[]) => Promise<any>>): Promise<any> {
        let head = Promise.resolve();

        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < promiseArray.length; ++i) {
            head = head.then((e) => {
                return promiseArray[i](e);
            });
        }
        return head;
    }

    public static flowWithoutBreak(promiseArray: Array<(...args: any[]) => Promise<any>>): Promise<any> {
        let head = Promise.resolve();

        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < promiseArray.length; ++i) {
            head = head.then((e) => {
                // return promiseArray[i](e);
                return new Promise((resolve) => {
                    promiseArray[i]().then((e) => {
                        resolve(e);
                    }).catch((reason) => {
                        resolve(reason);
                    });
                });
            });
        }
        return head;
    }

    public static waitForSeconds(seconds: number): Promise<void> {
        return new Promise((resolve) => {
            setTimeout(() => { resolve(); }, seconds * 1000);
        });
    }
}
