import { BaseService } from "../../frame/BaseService";

export class BaseCommand implements ICommand {
    public params?: any;
    public options?: IExecuteOption;
    public succeed?: (e: IPXResponce) => void;
    public fail?: (reason: any) => void;
    public prevResult?: any;
    public service: IBaseService;
    public type: keyof ICommandType;
    public command: number;

    constructor(service: IBaseService, type: keyof ICommandType,
                command: number, params?: any, options?: IExecuteOption,
                succeed?: (e: IPXResponce) => void, fail?: (reason: any) => void) {
        this.service = service;
        this.type = type;
        this.command = command;
        this.params = params;
        this.options = options;
        this.succeed = succeed;
        this.fail = fail;
    }

    public isValidCommand(): boolean {
        return this.type === 0 || this.type === 1;
    }

    public makePromise(): Promise<any> {

        let promise = null;
        if (this.type === 0) {
            promise = (this.service as BaseService).doMakeExecutePromise(this.command, this.params, this.options);
        } else if (this.type === 1) {
            promise = (this.service as BaseService).doMakeInfoPromise(this.command, this.params, this.options);
        }
        return promise;
    }
}
