/**
 * 答题器的状态
 */
export enum ANSWER_STATUS {
    // 内部原因，需要等待下一次结果
    SOCKET = "Socket",
    // 没有设备
    NO_DEVICE = "NoDevice",
    // 电源未插
    NO_POWER = "NoPower",
    // IP没有配置
    NO_IP = "NoIP",
    // USB断开
    USB_OFF = "USBOff",
    // 正常可用
    READY = "ready",
}

export function getAnswerStatusCode(status: ANSWER_STATUS): number {
    switch (status) {
        case ANSWER_STATUS.READY:
            return 0;
        case ANSWER_STATUS.USB_OFF:
            return -1;
        case ANSWER_STATUS.NO_DEVICE:
            return -1;
        case ANSWER_STATUS.NO_POWER:
            return -1;
        case ANSWER_STATUS.SOCKET:
            return -3;
        case ANSWER_STATUS.NO_IP:
            return -2;
    }
}
