import { PXFileManager } from "./FileManager";
import { LocalFileOpenMode } from "../../defs/LocalFileOpenMode";
import { LocalFileService } from "../../services/LocalFileService";
/**
 * 代表一个文件对象
 * todo: 状态机来处理各种行为
 */
export class PXFile {

    private service: LocalFileService = null;
    private status: PXFileStatus = PXFileStatus.Closed;
    public error: any = null;

    constructor(private filePath: string, private mode: LocalFileOpenMode, private manager: PXFileManager) {
        this.service = this.manager.localFileService;
    }

    /**
     * 打开文件
     * @param mode 文件打开模式
     */
    public open(mode: LocalFileOpenMode): Promise<any> {

        if (this.status === PXFileStatus.Opened) {
            return this.close().then(() => {
                return this.doOpen(mode);
            }).then(() => {
                return Promise.resolve(this);
            }).catch(() => {
                return Promise.reject(this);
            });
        }

        if (this.status === PXFileStatus.Closed) {
            return this.doOpen(mode).then(() => {
                return Promise.resolve(this);
            }).catch(() => {
                return Promise.reject(this);
            });
        }

        if (this.status === PXFileStatus.Opening) {
            return new Promise((resolve) => {
                this.addOnOpened(() => {
                    resolve(this);
                });
            });
        }
    }

    /**
     * 关闭文件
     */
    public close(): Promise<any> {
        if (this.status === PXFileStatus.Opened || this.status === PXFileStatus.Opening) {
            return this.doClose();
        }
        return Promise.resolve();
    }

    /**
     * 读文件
     */
    public read(): Promise<string> {
        if (this.CanRead) {
            return this.doRead();
        } else {
            return this.open(LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_READ_ONLY).then(() => {
                return this.doRead();
            }).catch((reason) => {
                return Promise.reject(reason);
            });
        }
    }

    /**
     * 写文件
     * @param content
     */
    public write(content: string): Promise<any> {
        if (this.CanWrite) {
            return this.doWrite(content);
        } else {
            return this.open(this.mode).then(() => {
                return this.doWrite(content);
            });
        }
    }

    /**
     * 文件状态
     */
    public get Status(): PXFileStatus {
        return this.status;
    }
    public set Status(value: PXFileStatus) {
        this.status = value;
    }

    /**
     * 文件打开的模式
     */
    public get Mode(): LocalFileOpenMode {
        return this.mode;
    }

    /**
     * 文件路径
     */
    public get Path(): string {
        return this.filePath;
    }

    /**
     * 文件可读
     */
    public get CanRead(): boolean {
        return (this.mode === LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_READ_ONLY
            || this.mode === LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_READ_WRITE)
            && this.status === PXFileStatus.Opened;
    }

    /**
     * 文件可写
     */
    public get CanWrite(): boolean {
        return (this.mode === LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_READ_WRITE
            || this.mode === LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_WRITE_ONLY)
            && this.status === PXFileStatus.Opened;
    }

    private doOpen(mode: LocalFileOpenMode): Promise<any> {
        this.status = PXFileStatus.Opening;
        this.mode = mode;
        // console.warn("打开文件", this);
        return this.service.px_cmd_localfile_open({ path: this.filePath, mode }).then(() => {
            this.status = PXFileStatus.Opened;
            this.emitOpenEvent();
            return Promise.resolve(this);
        }).catch((reason) => {
            this.status = PXFileStatus.Closed;
            this.error = reason;
            return Promise.reject({ reason, file: this });
        });
    }

    private doClose(): Promise<any> {
        this.status = PXFileStatus.Closed;
        // console.warn("关闭文件", this);
        return this.service.px_cmd_localfile_close({ path: this.filePath }).then(() => {
            return Promise.resolve();
        }).catch((reason) => {
            this.error = reason;
        });
    }

    private doRead(): Promise<string> {
        return this.service.px_cmd_localfile_read({ path: this.filePath }).then((e) => {
            return Promise.resolve(e.result.rsp_data);
        }).catch((reason) => {
            this.error = reason;
        });
    }

    private doWrite(content: string): Promise<any> {
        console.log("写入文件", content);
        return this.service.px_cmd_localfile_write({ path: this.filePath, content }).catch((reason) => {
            this.error = reason;
        });
    }

    private openCallback: Array<() => void> = [];
    /**
     * 添加文件打开时的回调
     * @param cb 回调
     */
    public addOnOpened(cb: () => void) {
        this.openCallback.push(cb);
    }
    /**
     * 触发打开文件事件
     */
    private emitOpenEvent(): void {
        this.openCallback.forEach((cb) => {
            cb();
        });
        this.openCallback.length = 0;
    }
}

export enum PXFileStatus {
    Closed = 0,
    Opening,
    Opened,
}
