    /**
     * 注意: 调用该方法前需要先运行关注度检测
     */
    public changePose(args: any, poseHandler: (eventInfo: any) => void, playerAreaHandler: (eventInfo: any) => void): Promise<any> {
        // if (this.onceCallerRequestIds.changePoseRequestId !== undefined) {
        //     throw new Error("还未调用stopPose");
        // }
        const aiService = this.getService(AIService) as AIService;
        const options = aiService.makeExecuteOption(null, null, (uuid: string) => {
            this.pairCallerRequestIds.changePoseRequestId = uuid;
        }, 2000);
        options.addListener(AIMessage.PX_MSG_AI_FUNCTIONGAME_RESULT, {
            handler: (msg, eventInfo, type, requestId) => {
                poseHandler(eventInfo);
            },
        });
        options.addListener(AIMessage.PX_MSG_AI_FUNCTIONGAME_SHOWREGION, {
            handler: (msg, eventInfo, type, requestId) => {
                playerAreaHandler(eventInfo);
            },
        });
        return aiService.px_cmd_ai_start_functiongame(args, options);
    }
    public stopPoseDetect(): Promise<any> {
        // if (this.onceCallerRequestIds.changePoseRequestId === undefined) {
        //     throw new Error("还未调用 changePose方法");
        // }
        const aiService = this.getService(AIService) as AIService;
        aiService.clearListeners(this.pairCallerRequestIds.changePoseRequestId + "_" + AIMessage.PX_MSG_AI_FUNCTIONGAME_RESULT);
        aiService.clearListeners(this.pairCallerRequestIds.changePoseRequestId + "_" + AIMessage.PX_MSG_AI_FUNCTIONGAME_SHOWREGION);
        delete this.pairCallerRequestIds.changePoseRequestId;
        return aiService.px_cmd_ai_stop_functiongame();
    }
