    /**
     * 获取学生头像信息
     * @returns headInfo[]  返回一组头像信息列表
     * headInfo: {
     *  id:      识别id
     *  headimg: 识别到的学生头像
     *  x:       学生头像的x坐标
     *  y:       学生头像的y坐标
     *  width:   学生头像的宽
     *  height:  学生头像的高
     * }
     */
    public checkoutStudentHead(stuID: string = ""): Promise<{
        image: string;
        galleries: IHeadGalleryData[];
    }> {
        const aiService = this.getService(AIService) as AIService;
        const param: any = {};
        if (stuID === "" || stuID === undefined || stuID === null) {
        } else {
            param.stuID = stuID;
        }
        if (aiService.openStatus === SERVICE_OPEN_STATUS.OPENED) {
            return new Promise((resolve, reject) => {
                const options = aiService.makeExecuteOption();
                options.addOnceListener(AIMessage.PX_MSG_AI_GALLERY, {
                    handler: (msg, eventInfo, type, requestId) => {
                        resolve(eventInfo);
                    },
                });
                aiService.px_cmd_ai_fetch_gallery(param, options).catch((e) => {
                    reject(e);
                });
            });
        } else {
            return Promise.reject("AIService未打开");
        }
    }

