    private concentrationStarted: boolean = false;
    public startConcerntration(): Promise<any> {
        if (this.concentrationStarted) {
            return Promise.resolve();
        }
        this.concentrationStarted = true;
        const service = this.getService(AIService) as AIService;
        const options = service.makeExecuteOption(null, null, (uuid) => {
            this.pairCallerRequestIds.concerntrationRequestId = uuid;
        });
        options.addListener(AIMessage.PX_MSG_AI_CONCERNTRATION, {
            handler: (msg, eventInfo, type, requestId) => {
                this.emitServiceMessage({
                    cmd: "concerntration",
                    data: eventInfo,
                });
            },
        });
        return service.px_cmd_ai_start_concerntration(null, options);
    }

    public stopConcerntration(): Promise<any> {
        if (this.concentrationStarted === false) {
            return Promise.resolve();
        }
        this.concentrationStarted = false;
        const service = this.getService(AIService) as AIService;
        service.clearListeners(this.pairCallerRequestIds.concerntrationRequestId + "_" + AIMessage.PX_MSG_AI_CONCERNTRATION);
        return service.px_cmd_ai_stop_concerntration();
    }

    /**
     * 开启或关闭举手检测
     * @param state 1:开启 0:关闭
     * @param onHandupResult 举手信息回调
     */
    public startAcceptData(state: number,
        onHandupResult?: (handupStudents: Array<{
            stuID: string, // 学生ID
            state: number, // 举手状态
            x: number, // 位置
            y: number,
            width: number,
            height: number,
        }>) => void): Promise<any> {
        const aiservice = this.getService(AIService) as AIService;
        if (state === 1) {
            return new Promise((resolve) => {
                const option: IExecuteOption = aiservice.makeExecuteOption(
                    null, null,
                    (uuid) => { this.pairCallerRequestIds.handupRequestId = uuid; },
                );
                option.addListener(AIMessage.PX_MSG_AI_HANDUP, {
                    handler: (msg, eventInfo, type, requestId) => {
                        onHandupResult(eventInfo);
                    },
                });
                return aiservice.px_cmd_ai_start_handup({ handupType: 0 }, option).then((e) => {
                    resolve(e);
                });
            });
        } else {
            aiservice.clearListeners(this.pairCallerRequestIds.handupRequestId + "_" + AIMessage.PX_MSG_AI_HANDUP);
            return aiservice.px_cmd_ai_stop_handup({ handupType: 0 }).then((e) => {
                delete this.pairCallerRequestIds.handupRequestId;
                return Promise.resolve();
            }).catch((reason) => {
                return Promise.reject({ code: -1, reason });
            });
        }
    }

