    /**
     *
     * 注意: 调用该方法前需要先运行关注度检测
     */
    public startTracking(stuID: string, onTracking: (...args: any[]) => void): Promise<any> {
        const service = this.getService<AIService>(AIService);
        const exeOption: IExecuteOption = service.makeExecuteOption();
        exeOption.requestIdCallback = (uuid: string) => { this.pairCallerRequestIds.trackingRequestId = uuid; console.warn("开始追踪", this.pairCallerRequestIds.trackingRequestId); };
        exeOption.addListener(AIMessage.PX_MSG_AI_TRACKING, {
            handler: (msg, eventInfo, type, requestId) => {
                onTracking(eventInfo);
            },
        });
        return service.px_cmd_ai_start_tracking({ stuID }, exeOption);
    }

    public stopTracking(stuID: string): Promise<any> {
        const aiService = this.getService(AIService) as AIService;
        aiService.clearListeners(this.pairCallerRequestIds.trackingRequestId + "_" + AIMessage.PX_MSG_AI_TRACKING);
        console.warn("停止追踪", this.pairCallerRequestIds.trackingRequestId);
        delete this.pairCallerRequestIds.trackingRequestId;
        return aiService.px_cmd_ai_stop_tracking({ stuID });
    }
