    /**
     * 发送消息
     * CLIENT_MESSAGE.CAMERA_CHANGE 相机插拔状态
     * {
     *    cmd: CLIENT_MESSAGE.CAMERA_CHANGE,
     *    devices, //当前可用的相机列表
     * }
     */
    public showCamera(): Promise<any> {
        const cameraService = this.getService(CameraService) as CameraService;
        let cameralist: string[] = null;
        // 链式调用
        Log.log("开始获取相机列表", Date.now());
        return Promise.resolve().then(() => {
            return cameraService.px_inf_camera_list();
        }).then((list: string[]) => {
            Log.log("取到相机列表", list);
            cameralist = list;
        }).then(() => {
            Log.log("设置相机", cameralist[0]);
            return cameraService.px_cmd_camera_set_camera(cameralist[0]);
        }).then(() => {
            Log.log("显示");
            return cameraService.px_cmd_camera_preview(false);
        }).then(() => {
            Log.log("设置分辨率");
            const param: IChangeVideoSettingParam = { width: this.designWidth, height: this.designHeight };
            return cameraService.px_cmd_camera_change_video_setting(param);
        }).catch((e: IPXResponce) => {
            Log.warn(e);
            e.bubble = false;
            return Promise.reject(e);
        });
    }

    public setCamera(name: string): Promise<any> {
        const service = this.getService<CameraService>(CameraService);
        return service.px_cmd_camera_set_camera(name);
    }

    /**
     *
     * 开启摄像头截图
     *
     * @param x
     * @param y
     * @param width
     * @param height
     */
    public startCameraCapture(x: number, y: number, width: number, height: number, callback: FnCallback): Promise<any> {
        if (x < 0) {
            x = 0;
        }
        if (y < 0) {
            y = 0;
        }

        if (x + width > this.designWidth) {
            x = this.designWidth - width;
        }

        if (y + height > this.designHeight) {
            y = this.designHeight - height;
        }

        const service = this.getService<CameraService>(CameraService);
        if (this.pairCallerRequestIds.cameraCaptureRequestId !== undefined) {
            return Promise.resolve();
        }
        const options = service.makeExecuteOption(null, null, (uuid) => {
            this.pairCallerRequestIds.cameraCaptureRequestId = uuid;
        });
        options.addListener(CameraMessage.PX_MSG_CAMERA_PRESENT, {
            handler: (msg, eventInfo, type, requestId) => {
                callback(eventInfo);
                this.emitServiceMessage({
                    cmd: "studentImage",
                    imghead: eventInfo,
                });
            },
        });
        return service.px_cmd_camera_capture({ stop_flag: false, x, y, width, height }, options).then(() => {
            return Promise.resolve();
        });
    }

    public stopCameraCapture(): Promise<any> {
        const service = this.getService<CameraService>(CameraService);
        if (this.pairCallerRequestIds.cameraCaptureRequestId === undefined) {
            return Promise.resolve();
        }

        service.clearListeners(this.pairCallerRequestIds.cameraCaptureRequestId + "_" + CameraMessage.PX_MSG_CAMERA_PRESENT);
        return service.px_cmd_camera_capture({ stop_flag: true, x: 0, y: 0, width: 0, height: 0 }).then(() => {
            delete this.pairCallerRequestIds.cameraCaptureRequestId;
            return Promise.resolve();
        });
    }
