    /**
     * 解绑答题器
     * @param type 0 解绑单个， 1 解绑所有
     * @param studentId 学生id
     */
    public unBindStudent(type: number, studentId?: string): Promise<any> {
        const service = this.getService(AnswerToolService) as AnswerToolService;
        const param: {
            user_id: string;
        } = {
            user_id: "0",
        };
        if (type === 0) {
            param.user_id = studentId;
            return service.px_cmd_answer_remove(param);
        } else {
            const arrs: Array<Promise<any>> = [];
            const ids: string[] = [];
            this.answerToolBinding.bindedStudents.forEach((bind) => {
                ids.push(bind.user_id);
            });
            ids.forEach((id) => {
                arrs.push(service.px_cmd_answer_remove({ user_id: id }).then(() => {
                    this.answerToolBinding.removeBindInfo(id);
                }));
            });
            return Promise.all(arrs);
        }
    }
