    private settingBuffer: BufferedQuery = null;
    /**
     * 获取web_config.ini的配置
     * @param key 要获取的key
     * @param group 要获取key的group
     * @param cb 获取后的回调
     */
    public getSettings(key: string, group: string): Promise<string> {
        return new Promise((resolve, reject) => {
            if (this.settingBuffer === null) {
                this.settingBuffer = new BufferedQuery(() => {
                    return this.readXml("./web_config.ini").then((data: string) => {
                        const settings = RegExpHelper.parseINIString(data);
                        return Promise.resolve(settings);
                    });
                });
            }
            this.settingBuffer.doQuery(() => {
                const settings = this.settingBuffer.getTarget();
                if (settings[group] && settings[group][key]) {
                    resolve(settings[group][key]);
                } else {
                    reject(new Error(`setting[${group}][${key}]不存在`));
                }
            });
        });
    }

    /**
     * 获取页面路径
     */
    public getWebPath(): Promise<string> {
        const location = window.location;
        if (location.protocol !== "file:") {
            return Promise.reject(new Error("路径不是file协议"));
        } else {
            const pathname = location.pathname;
            return Promise.resolve(RegExpHelper.getFullPathDir(pathname));
        }
    }

