    /**
     * 获取指定目录信息
     * @param dirPath dirPath
     * @param calcSize 是否获取当前目录的大小
     * @param recursive 是否遍历子目录
     * @returns Promise参数 { entryList: string[], totalSize: number}
     */
    public getDirInfo(dirPath: string, calcSize: boolean = false, recursive: boolean = false): Promise<{
        count?: number,
        entryList: string[],
        exists?: boolean,
        totalSize: number,
    }> {
        const service = this.getService(LocalFileService) as LocalFileService;
        return service.px_inf_localfile_filelist({
            dir: dirPath,
            dir_size: calcSize,
            sub_files: recursive,
        }).then((res) => {
            return Promise.resolve({
                entryList: res.files,
                totalSize: res.dir_size,
            });
        }).catch((reason) => {
            if (reason.result.rsp_error === LocalFileError.PX_ERR_LOCALFILE_DIR_NOT_FOUND) {
                reason.bubble = false;
                return Promise.resolve({
                    count: 0,
                    entryList: [],
                    exists: false,
                    totalSize: 0,
                });
            } else {
                return Promise.reject(reason);
            }
        });
    }
