    /**
     * 创建xml文件
     * @param type 0 覆盖 1追加
     * @param fileName 文件名称,需带后缀
     * @param content 文件内容
     * @param path 文件路径
     * @param cb 回调函数
     */
    public createXml(type: number, fileName: string, content: string, path: string): Promise<any> {
        console.log("createXml", type, fileName, content, path);
        let mode = LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_APPEND;
        if (type === 0) {
            mode = LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_WRITE_ONLY;
        }
        const absolutePath = path + "/" + fileName;
        let file2: PXFile = null;
        return this.fileManager.open(absolutePath, mode).then((file) => {
            file2 = file;
        }).then(() => {
            return file2.write(content);
        }).then(() => {
            return file2.close();
        });
    }
    public readXml(filePath: string): Promise<string> {
        console.log("readXml", filePath);
        const mode = LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_READ_ONLY;
        let text = "";
        let file: PXFile = null;
        return this.fileManager.open(filePath, mode).then((file2) => {
            file = file2;
        }).then(() => {
            return file.read();
        }).then((content) => {
            text = content;
            console.log(filePath, "读取内容: ", text);
        }).then(() => {
            file.close();
            return Promise.resolve(text);
        }).catch((err) => {
            return Promise.reject(err);
        });
    }

    /**
     * 将json对象保存成文件
     * @param filePath
     * @param jsonData
     * @param cb 回调 返回参数 {suss: boolean, reason?: any} 若成功则suss为true 失败则suss为false, reason
     */
    public saveJsonData(filePath: string, jsonData: object): Promise<{ suss: boolean, reason?: any }> {
        return new Promise((resolve) => {
            const str = JSON.stringify(jsonData, null, 2);
            const mode = LocalFileOpenMode.PX_LOCALFILE_OPEN_FILE_MODE_WRITE_ONLY;
            const absolutePath = filePath;
            let file2: PXFile = null;
            this.fileManager.open(absolutePath, mode).then((file) => {
                file2 = file;
            }).catch((file: PXFile) => { // todo: 这里为什么会打开错误
                if (file.error.result.rsp_error === LocalFileError.PX_ERR_LOCALFILE_OPENED) {
                    return Promise.resolve();
                }
            }).then(() => {
                return file2.write(str);
            }).then(() => {
                return file2.close();
            }).then(() => {
                resolve({ suss: true });
            }).catch((reason) => {
                resolve({ suss: false, reason });
            });
        });
    }
