    /**
     * 重命名文件列表
     * @param files 文件列表[{dst: '', src: ''}, ...]
     * @return 重命结果 [{dst: '', src: '', result: true|false}, ...]
     */
    public renameFiles(files: Array<{
        key?: string;
        dst: string;
        src: string;
    }>): Promise<{
        list: Array<{
            dst: string;
            src: string;
            result: boolean;
        }>;
    }> {
        const service = this.getService(LocalFileService) as LocalFileService;
        const result: Array<{
            dst: string;
            src: string;
            result: boolean;
        }> = [];
        const arrPromise: Array<Promise<any>> = [];
        files.forEach((file) => {
            arrPromise.push(service.px_cmd_localfile_rename({ file_name: file.src, new_name: file.dst }).then(() => {
                result.push({ dst: file.dst, src: file.src, result: true });
                return Promise.resolve();
            }).catch(() => {
                result.push({ dst: file.dst, src: file.src, result: false });
                return Promise.resolve();
            }));
        });
        return Promise.all(arrPromise).then(() => {
            return Promise.resolve({ list: result });
        });
    }
    /**
     *
     * @param filemap
     */
    public checkFile(filemap: {
        [key: string]: string;
    }): Promise<{
        [key: string]: boolean;
    }> {
        const service = this.getService(LocalFileService) as LocalFileService;
        const promiseArray: Array<Promise<any>> = [];
        const result: {
            [key: string]: boolean;
        } = {};
        for (const key in filemap) {
            if (true) {
                promiseArray.push(service.px_inf_localfile_fileinfo(filemap[key]).then(() => {
                    result[key] = true;
                }).catch((reason) => {
                    result[key] = false;
                    return Promise.resolve(key);
                }));
            }
        }
        return Promise.all(promiseArray).then(() => {
            return Promise.resolve(result);
        });
    }
