    /**
     * 检查目标路径targetPath中是否存在与fileList中同名的文件
     * @param fileList 文件列表
     * @param targetPath 目标路径
     * @param callback 回调 返回 同名文件列表 (res: {exist: string[]}) => void
     */
    public searchExistFiles(fileList: string[], targetPath: string): Promise<{ exist: string[] }> {
        return new Promise((resolve) => {
            const exist: string[] = [];
            const filenameList: string[] = [];
            fileList.forEach((file) => {
                filenameList.push(Path.getFileName(file));
            });
            const promiseArray: Array<Promise<any>> = [];
            filenameList.forEach((filename) => {
                promiseArray.push(
                    new Promise((resolve2) => {
                        const filePath = targetPath + "/" + filename;
                        this.fileExist(filePath).then((fileExist: boolean) => {
                            if (fileExist) {
                                exist.push(filename);
                            }
                            resolve2();
                        });
                    }),
                );
            });
            Promise.all(promiseArray).then(() => {
                resolve({ exist });
            });
        });
    }

    /**
     * 导入文件
     * @param from 源文件
     * @param to 目标文件
     * @param type 0 取消 1 覆盖 2 跳过
     * @param callback 返回 {path: string, code?: any, reason?: any}
     */
    public importFileEx(from: string, to: string, type: 0 | 1 | 2): Promise<{ path: string, code?: any, reason?: any }> {
        return new Promise((resolve) => {
            const service = this.getService<LocalFileService>(LocalFileService);
            if (type === 0) {
                resolve({ code: -2, path: from });
                return;
            }
            if (from === to) {
                console.error(`导入时源路径与目标路径相同:${from}`);
                resolve({path: from, reason: `导入时源路径与目标路径相同:${from}`});
                return;
            }
            service.px_cmd_localfile_cpfile({ src: from, dst: to, cover: type === 1 ? true : false }).then(() => {
                resolve({ path: to });
            }).catch((reason) => {
                resolve({ path: from, code: -1, reason });
            });
        });
    }

    /**
     * 导入文件
     * @param from 源文件列表
     * @param targetPath 目标目录 绝对路径
     * @param type 0 取消 1 覆盖 2 跳过
     * @param callback 回调参数
     *                   {
     *                     successList: string[], //导入成功的文件列表
     *                     failList: string[] //导入失败的文件列表
     *                   }
     *
     */
    public importFileMulti(from: string[], targetPath: string, type: 0 | 1 | 2): Promise<{ successList: string[], failList: string[] }> {
        return new Promise((resolve) => {
            while (targetPath.endsWith("/") || targetPath.endsWith("\\")) {
                targetPath = targetPath.substr(0, targetPath.length - 1);
            }
            console.log("导入文件", from, targetPath);
            const targetFiles: string[] = [];
            from.forEach((file) => {
                targetFiles.push(targetPath + "/" + Path.getFileName(file));
            });

            const promiseArray: Array<Promise<any>> = [];
            const results: any[] = [];
            const failures: string[] = [];
            const length = targetFiles.length;
            for (let i = 0; i < length; ++i) {
                promiseArray.push(
                    this.importFileEx(from[i], targetFiles[i], type).then((e: any) => {
                        if (e.code < 0) {
                            failures.push(e.path);
                        } else {
                            results.push(e.path);
                        }
                    }),
                );
            }

            Promise.all(promiseArray).then(() => {
                resolve({ successList: results, failList: failures });
            });
        });
    }

