    /**
     * 选择文件以准备拷贝到targetPath中
     * @param targetPath 目标路径 绝对路径
     * @param type 文件类型
     * @param multiSelect 是否多选
     * @param callback 成功回调参数
     *           {
     *             exist: string[], //重名文件列表
     *             notexist: string[] //不重名文件列表
     *           }
     *        失败回调参数
     *           {
     *             code: number,
     *             reason: string
     *           }
     */
    public selectFilesForImport(targetPath: string, type: "video" | "image" | "audio" | "json", multiSelect: boolean): Promise<{ exist: string[], notexist: string[] } | { code: number, reason: string }> {
        return new Promise((resolve) => {
            while (targetPath.endsWith("/") || targetPath.endsWith("\\")) {
                targetPath = targetPath.substr(0, targetPath.length - 1);
            }

            this.createDir(targetPath).then(() => {

                return this.openFileDialog(type, multiSelect).then((args) => {
                    const filelist = args.pathList;
                    if (filelist.length <= 0) {
                        console.log("未选中文件");
                        const output: { exist: string[], notexist: string[] } = {
                            exist: [],
                            notexist: [],
                        };
                        resolve(output);
                        return;
                    }
                    const srcPath = filelist[0].substr(0, filelist[0].lastIndexOf("/"));
                    console.log("源目录", srcPath);
                    console.log("选中文件", filelist);
                    this.searchExistFiles(filelist, targetPath).then((existFiles: { exist: string[] }) => {
                        const existFullPaths: string[] = [];
                        const output: { exist: string[], notexist: string[] } = {
                            exist: null,
                            notexist: null,
                        };
                        existFiles.exist.forEach((existFileName) => {
                            existFullPaths.push(srcPath + "/" + existFileName);
                        });
                        const notDuplicatedFiles: string[] = this.exclude(filelist, existFullPaths);
                        console.log("不重复的文件", notDuplicatedFiles);
                        output.exist = existFiles.exist;
                        output.notexist = notDuplicatedFiles;
                        resolve(output);
                    });
                });
            }).catch((reason) => {
                resolve({ code: -1, reason });
            });
        });
    }

    /**
     * 返回 from-to的数组
     * @param from
     * @param to 是from数组的子集
     */
    private exclude(from: string[], to: string[]): string[] {
        const notDuplicatedItems: string[] = [];
        from.forEach((item) => {
            if (to.indexOf(item) < 0) {
                notDuplicatedItems.push(item);
            }
        });
        return notDuplicatedItems;
    }

