    /**
     * 上传文件到oss
     * @param filePaths 文件路径
     * @param progressCallback 上传进度回调
     *     {
     *          status: "uploading",
     *          progress: number, //单个文件的进度
     *          filePath: string, //当前上传的文件
     *          upLoadRequestId: string, //上传任务的uuid
     *     }
     */
    public uploadFileToOSS(filePaths: string[], progressCallback: FnCallback): Promise<any> {
        let upLoadRequestId = "";
        const urls: string[] = [];
        const files: string[] = [];
        const service = this.getService<LocalFileService>(LocalFileService);
        const options = service.makeExecuteOption(null, null, (requestId) => { upLoadRequestId = requestId; }, 0);
        const fileFinishListener: IListener = {
            handler: (msg, eventInfo, type, requestId) => {
                urls.push(eventInfo.url);
                files.push(eventInfo.file_name);
            },
        };
        const progressListener: IListener = {
            handler: (msg, eventInfo, type, requestId) => {
                progressCallback({
                    status: "uploading",
                    progress: eventInfo.transfered / eventInfo.total,
                    filePath: eventInfo.current_file,
                    upLoadRequestId,
                });
            },
        };
        options.addListener(LocalFileMessage.PX_MSG_LOCALFILE_OSS, fileFinishListener);
        options.addListener(LocalFileMessage.PX_MSG_LOCALFILE_OSS_PROGRESS, progressListener);
        return service.px_cmd_localfile_uploadoss({ file_paths: filePaths }, options).then((res) => {
            let code = 0;
            if (res.result.failed_files) {
                code = -1;
            }
            return Promise.resolve({
                code,
                filePath: files,
                url: urls,
                status: "finish",
                progress: 1,
                failedFiles: res.result.failed_files ? res.result.failed_files : [],
            });
        }).catch((reason) => {
            const result: IResult = reason.result;
            return Promise.reject(result);
        });
    }

