    /**
     * 读取视频信息
     * @param filePath 文件路径
     * @param scale 缩略图缩放倍数
     * @param type 0:获取应用执行路径 1:获取传入绝对路径
     * @param callback 回调 成功返回:
     *                 {
     *                   cmd: "readVideoInfo",
     *                   fileName: string,
     *                   fileSize: number,
     *                   createTime: string,
     *                   duration: number,
     *                   thumbnailImg: string,
     *                   h: number,
     *                   w: number
     *                 }
     *                     失败返回:
     *                 {
     *                 code: number
     *                 }
     */
    public readVideoInfo(filePath: string, scale: number = 8, type: 0 | 1): Promise<{
        code?: any,
        reason?: any,
    } | {
        cmd: string,
        fileName: string,
        fileSize: number,
        createTime: string,
        duration: number,
        thumbnailImg: string,
        h: number,
        w: number,
    }> {

        return new Promise((resolve) => {
            const service = this.getService<PXVideoProcessorService>(PXVideoProcessorService);
            const res: { [key: string]: any } = {};
            this.getAppPath().then((appPath) => {
                if (!appPath.endsWith("/") && !appPath.endsWith("\\")) {
                    appPath += "/";
                }
                if (type === 0) {
                    filePath = appPath + filePath;
                }

                service.px_cmd_videoprocessor_video_infor({ path: filePath, resize: scale }).then((e) => {
                    const data = e.result.rsp_data.data;
                    res.cmd = "readVideoInfo";
                    res.fileName = filePath;
                    res.fileSize = data.file_size;
                    res.createTime = data.create_time;
                    res.duration = data.duration;
                    res.thumbnailImg = data.thumbnail;
                    res.h = data.height;
                    res.w = data.width;
                    resolve(res);
                }).catch((reason) => {
                    if (reason && reason.result) {
                        resolve({ code: reason.result.rsp_error });
                    } else {
                        resolve({ code: -1, reason });
                    }
                });
            });
        });
    }

    /**
     * 视频平滑
     * @param slices preVideo 第一个视频的地址  nextVideo 第二个视频的地址 flag 0:将生成的帧插入到第一个视频的后面; 1:将生成的帧插入到第二个视频的前面
     * @param type 0：获取应用执行路径；1：获取传入绝对路径
     * @param cb 回调
     */
    public sliceSmoothing(slices: Array<{ preVideo: string, nextVideo: string, flag: number }>, type: number): Promise<any> {
        return new Promise((resolve) => {
            const details: Array<{ previous: string, behind: string, add2previous: boolean }> = [];
            this.getVideoPath().then((path) => {
                slices.forEach((slice) => {
                    let prev = slice.preVideo;
                    let next = slice.nextVideo;
                    const add2prev = slice.flag === 0 ? true : false;
                    if (type === 0) {
                        prev = path + "/" + prev;
                        next = path + "/" + next;
                    }
                    details.push({
                        previous: prev,
                        behind: next,
                        add2previous: add2prev,
                    });
                });
                const service = this.getService<PXVideoProcessorService>(PXVideoProcessorService);
                service.px_cmd_videoprocessor_smooth({
                    video_detail: details,
                }).then((e) => {
                    resolve(e);
                });
            });
        });
    }

