    private isRecordingScreen: boolean = false;
    /**
     * 开始采集相机视频
     * @param videoPath 指定录制保存的视频文件路径
     * @description
     *   在进行视频采集之前需要先调用 startCaptureDevice 来启动采集设备
     */
    public startCameraRecord(videoPath: string): Promise<any> {
        const service = this.getService<VideoCaptureService>(VideoCaptureService);
        const dirPath = Path.getDirPath(videoPath);
        if (this.isRecordingScreen === true) {
            return Promise.reject({ code: -2, reason: "已经开始录制了,请勿重复调用!" });
        }
        return this.createDir(dirPath).then(() => {
            return service.px_cmd_videocapture_startrecord(videoPath);
        }).then((e) => {
            this.isRecordingScreen = true;
            return Promise.resolve(e);
        }).catch((reason) => {
            return Promise.reject({ code: -1, reason });
        });
    }

    /**
     * 停止相机采集
     */
    public stopCameraRecord(filename: string): Promise<any> {
        const service = this.getService<VideoCaptureService>(VideoCaptureService);
        if (this.isRecordingScreen === false) {
            return Promise.reject({ code: -3, reason: "还未开始录制!" });
        }
        return service.px_cmd_videocapture_stoprecord(filename).then((e) => {
            this.isRecordingScreen = false;
            return Promise.resolve(e);
        });
    }

    /**
     * 启动采集设备
     * @param videoInput 视频输入设备
     * @param audioInput 音频输入设备
     * @description
     *   在进行视频采集之前需要先调用 startCaptureDevice 来启动采集设备
     */
    public startCaptureDevice(videoInput: string, audioInput: string): Promise<any> {
        const service = this.getService<VideoCaptureService>(VideoCaptureService);
        return service.px_cmd_videocapture_startcapture({
            video_device_name: videoInput,
            audio_device_name: audioInput,
        }).catch((e: IPXResponce) => {
            if (e.result.rsp_error === VideoCaptureError.PX_ERR_VIDEOCAPTURE_DEVICERUNING) {
                return Promise.resolve(e);
            }
            return Promise.reject(e);
        });
    }

