    /**
     * 将名称为name的显示层移至顶层 MainPage永远是最靠前的一层
     * @param name
     */
    public bringLayertoTop(name: string): Promise<any> {
        console.warn(`将${name}的显示层移至顶层`);
        return this.setLayerIndex(name, 0);
    }

    /**
     * 将名称为name的显示层移至底层 MainPage永远是最靠前的一层
     * @param name
     */
    public takeLayerToBack(name: string): Promise<any> {
        console.warn(`将${name}的显示层移至底层`);
        return this.getLayers().then((list) => {
            return this.setLayerIndex(name, list.length);
        });
    }

    /**
     * 设置名称为name的显示层的层数
     * @param name
     * @param index 0为最顶层,1在0之后,以此类推
     */
    public setLayerIndex(name: string, index: number): Promise<any> {
        console.warn("设置layerIndex");
        const serviceManager = this.getService<ServiceManager>(ServiceManager);
        return this.getLayers().then((list) => {
            const oldIndex = list.indexOf(name);
            if (oldIndex >= 0) {
                list.splice(oldIndex, 1); // 删除原有的name
                list.splice(index, 0, name); // 插入到指定位置
                return serviceManager.px_cmd_servicemanager_reorder_layers(list);
            }
        });
    }

    /**
     * 关闭显示层
     * @param name
     */
    public closeLayer(name: string): Promise<any> {
        return this.getLayers().then((layers) => {
            if (layers.indexOf(name) >= 0) {
                return this.updateLayerRect(0, 0, this.designWidth, this.designHeight, this.clientWidth + 1, 0, 1, 1, name);
            }
        });
    }

