    /**
     * 更新学生头像
     * @param u
     * @param v
     * @param uvWidth
     * @param uvHeight
     * @param x
     * @param y
     * @param width
     * @param height
     */
    public updateStudentImage(u: number, v: number, uvWidth: number, uvHeight: number, x: number, y: number, width: number, height: number): Promise<any> {
        if (u < 0) {
            u = 0;
        }
        if (v < 0) {
            v = 0;
        }
        if (u + uvWidth > this.designWidth) {
            u = this.designWidth - uvWidth;
        }
        if (v + uvHeight > this.designHeight) {
            v = this.designHeight - uvHeight;
        }
        return this.updateLayerRect(u, v, uvWidth, uvHeight, x, y, width, height, "Camera");
    }

    /**
     * 更新视频位置
     * @param x
     * @param y
     * @param width
     * @param height
     */
    public updateVideoRect(x: number, y: number, width: number, height?: number) {
        if (typeof height !== "number") {
            height = width * this.clientHeight / this.clientWidth;
        }
        return this.updateLayerRect(0, 0, this.designWidth, this.designHeight, x, y, width, height, "Player");
    }

    /**
     * 更新采集视频位置
     * @param x
     * @param y
     * @param width
     * @param height
     */
    public updateVideoCaptureRect(x: number, y: number, width: number, height?: number) {
        if (typeof height !== "number") {
            height = width * this.designHeight / this.designWidth;
        }
        return this.updateLayerRect(0, 0, this.designWidth, this.designHeight, x, y, width, height, "videocapture");
    }

    /**
     * 更新Layer的Rect
     * @param u 纹理横坐标
     * @param v 纹理纵坐标
     * @param uvWidth 纹理宽
     * @param uvHeight 纹理高
     * @param x 显示横坐标
     * @param y 显示纵坐标
     * @param width 显示宽
     * @param height 显示高
     * @param layer layer名称 Camera Player MainPage videocapture...
     */
    public updateLayerRect(u: number, v: number, uvWidth: number, uvHeight: number, x: number, y: number, width: number, height: number, layer: string): Promise<any> {
        const verticesData = this.makeVerticesDataFromScreenRect(u, v, uvWidth, uvHeight, x, y, width, height, layer);
        return this.updateVertices(verticesData);
    }
    /**
     * 更新顶点属性数据
     * @param vertices
     */
    public updateVertices(vertices: IVerticesData): Promise<any> {
        const serviceManager = this.getService(ServiceManager) as ServiceManager;
        if (serviceManager.openStatus !== SERVICE_OPEN_STATUS.OPENED) {
            return;
        }
        Log.log(vertices);
        return serviceManager.px_cmd_servicemanager_change_show(vertices);
    }
    /**
     *
     * @param u 纹理横坐标 范围[0-1920]
     * @param v 纹理纵坐标 范围[0-1080]
     * @param uvWidth 纹理宽
     * @param uvHeight 纹理高
     * @param x 顶点横坐标 范围[0-1920]
     * @param y 顶点纵坐标 范围[0-1080]
     * @param width 顶点宽
     * @param height 顶点高
     */
    public makeVerticesDataFromScreenRect(u: number, v: number, uvWidth: number, uvHeight: number, x: number, y: number, width: number, height: number, layer: string): IVerticesData {
        const screenWidth = this.clientWidth;
        const screenHeight = this.clientHeight;
        function mapToNormal(data: number) {
            return data * 2 - 1;
        }
        // 以左上角为原点计算纹理坐标
        const texLeft = u / this.designWidth;
        const texRight = (u + uvWidth) / this.designWidth;
        const texBottom = (v + uvHeight) / this.designHeight;
        const texTop = v / this.designHeight;
        // 以左下角为原点计算顶点位置
        const posLeft = x / screenWidth;
        const posRight = (x + width) / screenWidth;
        const posBottom = 1 - (y + height) / screenHeight;
        const posTop = 1 - y / screenHeight;
        const uvs: IVector2[] = [
            {
                x: texLeft,
                y: texBottom,
            },
            {
                x: texRight,
                y: texBottom,
            },
            {
                x: texRight,
                y: texTop,
            },
            {
                x: texLeft,
                y: texTop,
            },
        ];
        const positions: IVector3[] = [
            {
                x: mapToNormal(posLeft),
                y: mapToNormal(posBottom),
                z: 0,
            },
            {
                x: mapToNormal(posRight),
                y: mapToNormal(posBottom),
                z: 0,
            },
            {
                x: mapToNormal(posRight),
                y: mapToNormal(posTop),
                z: 0,
            },
            {
                x: mapToNormal(posLeft),
                y: mapToNormal(posTop),
                z: 0,
            },
        ];
        const vertices: IVerticesData = {
            vertices: {
                // 以左下角为原点
                uvs: Vector2.toArray(uvs),
                positions: Vector3.toArray(positions),
                indices: [0, 2, 1, 0, 3, 2],
            },
            name: layer,
        };
        return vertices;
    }

