export class ExecuteOption implements IExecuteOption {
    public timeout?: number;
    public onError?: (e: IPXResponce) => void;
    public msgBinds?: Map<number, IListener> = new Map();
    public onceMsgBinds?: Map<number, IListener> = new Map();
    public requestIdCallback?: (uuid: string) => void;
    public addListener(msg: number, listener: IListener): void {
        this.msgBinds.set(msg, listener);
    }
    public addOnceListener(msg: number, listener: IListener): void {
        this.onceMsgBinds.set(msg, listener);
    }
}
