export type FnPXCommandFinish = (uuid: string, result: IResult) => void;
export type FnPXError = (resp: IPXResponce) => void;
export type FnPXMessage = (msg: number, eventInfo: any, type: string, requestID: string) => void;

export class AIBrowser implements IAIBrowser {

    ////////////////
    public jsbridge: any;
    public onMessageCallback: FnPXMessage;
    public onFinishCommandCallback: FnPXCommandFinish;
    public commandCallbackMap: Map<string, FnPXCommandFinish> = new Map<string, FnPXCommandFinish>();
    public messageCallbackMap: Map<string | number, IListener[]> = new Map<string | number, IListener[]>();
    ////////////////

    public VERSION: string;

    public init(): Promise<any> {

        return new Promise((resolve, reject) => {
            if (window.qt !== undefined) {
                if (window.webChannel === undefined) {
                    window.webChannel = new QWebChannel(window.qt.webChannelTransport, (channel) => {
                        this.jsbridge = channel.objects.jsbridge;
                        this.onMessageCallback = this.onMessage.bind(this);
                        this.onFinishCommandCallback = this.onFinishCommand.bind(this);
                        this.jsbridge.onPXMessage_JS.connect(this.onMessageCallback);
                        this.jsbridge.onPXFinishCommand_JS.connect(this.onFinishCommandCallback);
                        resolve();
                    });
                } else {
                    this.jsbridge.onPXMessage_JS.connect(this.onMessageCallback);
                    this.jsbridge.onPXFinishCommand_JS.connect(this.onFinishCommandCallback);
                    resolve();
                }
            } else {
                console.error("qt对象获取失败");
                reject(new Error("qt对象获取失败"));
            }
        });

    }

    public release(): void {
        this.jsbridge.onPXMessage_JS.disconnect(this.onMessageCallback);
        this.jsbridge.onPXFinishCommand_JS.disconnect(this.onFinishCommandCallback);
    }

    /**
     * 打开服务
     * @param servicename
     * @param callback
     */
    public openService(servicename: string, timeout: number, success: FnPXResponce, fail: FnPXResponce): void {

        if (this.jsbridge) {
            this.jsbridge.open_JS(servicename, timeout, (uuid: string) => {
                console.log(`[${servicename}] "open" [${uuid}]`);
                this.commandCallbackMap.set(uuid, (auuid: string, result: IResult) => {
                    if (result.rsp_error < 0 && result.rsp_error !== -5) {
                        fail({ servicename, uuid, result });
                    } else {
                        success({ servicename, uuid, result });
                    }
                });
            });
        }
    }

    /**
     * 查询服务
     * @param servicename
     * @param category
     * @param params
     * @param callback
     */
    public getServiceInfo(servicename: string, category: number, params: any, opt: IExecuteOption, success: FnPXResponce, fail: FnPXResponce): void {
        if (params === undefined || params === null) {// 不可以传 undefined或null 否则会崩溃
            params = {};
        }
        if (this.jsbridge) {
            this.jsbridge.info_JS(servicename, category, params, opt.timeout, (uuid: string) => {
                console.log(`[${servicename}] "info" [${category}] [${uuid}]`, params, opt);
                this.commandCallbackMap.set(uuid, (auuid: string, result: IResult) => {
                    this.doCommandFinish(servicename, category, params, auuid, result, opt, success, fail);
                });
            });
        }
    }

    /**
     * 执行服务
     * @param servicename
     * @param command
     * @param params
     * @param callback
     */
    public executeService(servicename: string, command: number, params: any, requestIdCallback: (uuid: string) => void, opt: IExecuteOption, success: FnPXResponce, fail: FnPXResponce): void {
        if (params === undefined || params === null) {// 不可以传 undefined或null 否则会崩溃
            params = {};
        }
        if (this.jsbridge) {
            this.jsbridge.execute_JS(servicename, command, params, opt.timeout, (uuid: string) => {
                console.log(Date.now(), `[${servicename}] "execute" [${command}] [${uuid}]`, params, opt);
                if (typeof opt.requestIdCallback === "function") {
                    opt.requestIdCallback(uuid);
                }
                if (typeof requestIdCallback === "function") {
                    requestIdCallback(uuid);
                }

                this.commandCallbackMap.set(uuid, (auuid: string, result: IResult) => {
                    this.doCommandFinish(servicename, command, params, auuid, result, opt, success, fail);
                });
            });
        }
    }

    /**
     * 关闭服务
     * @param servicename
     * @param callback
     */
    public closeService(servicename: string, timeout: number, success: FnPXResponce, fail: FnPXResponce): void {
        if (this.jsbridge) {
            this.jsbridge.close_JS(servicename, timeout, (uuid: string) => {
                console.log(`[${servicename}] "close" [${uuid}]`);
                this.commandCallbackMap.set(uuid, (auuid: string, result: IResult) => {
                    if (result.rsp_error < 0) {
                        fail({ servicename, uuid: auuid, result });
                    } else {
                        success({ servicename, uuid: auuid, result });
                    }
                });
            });
        }
    }

    public onError(e: IPXResponce): void {
        console.warn(Date.now(), `aibrowser处理错误 ${e.servicename} ${e.cmd} param: ${JSON.stringify(e.param)} ${e.uuid} ${JSON.stringify(e.result)}`);
        e.bubble = false;
    }

    public addListener(token: string | number, listener: IListener): void {
        if (!this.messageCallbackMap.has(token)) {
            this.messageCallbackMap.set(token, []);
        }
        this.messageCallbackMap.get(token).push(listener);
    }

    public addOnceListener(token: string | number, listener: IListener): void {
        const magic: IListener = {
            handler: (msg: number, eventInfo: any, type: string, requestID: string) => {
                listener.handler.call(listener.context, msg, eventInfo, type, requestID);
                this.removeListener(token, magic);
            },
            context: listener.context,
        };
        this.addListener(token, magic);
    }
    public clearListeners(token: string | number): void {
        this.messageCallbackMap.delete(token);
    }
    public removeListener(token: string | number, listener: IListener): void {
        if (this.messageCallbackMap.has(token)) {
            const listeners = this.messageCallbackMap.get(token);
            const index = listeners.indexOf(listener);
            if (index >= 0) {
                listeners.splice(index, 1);
            }
        }
    }

    public emit(token: string | number, msg: number, eventInfo: any, type: string, requestID: string) {
        const listeners = this.messageCallbackMap.get(token);
        if (listeners === undefined) {
            return;
        }
        listeners.forEach((listener) => {
            listener.handler.call(listener.context, msg, eventInfo, type, requestID);
        });
    }

    private onMessage(msg: number, eventInfo: any, type: string, requestID: string) {
        // console.log(Date.now(), "message: ", msg, type, eventInfo, "requestId: ", requestID);
        if (type === "service" || type === "") { // 触发service消息
            this.emit(msg, msg, eventInfo, type, requestID);
            return;
        }
        if (type === "execute") {
            // 触发execute消息
            const token = requestID + "_" + msg;
            this.emit(token, msg, eventInfo, type, requestID);
        }
    }

    private onFinishCommand(uuid: string, result: IResult) {
        console.log(Date.now(), "finishCommand", uuid, result);
        this.commandCallbackMap.get(uuid)(uuid, result);
        this.commandCallbackMap.delete(uuid);
    }

    /**
     *
     * @param servicename 服务名称
     * @param command 命令码
     * @param auuid uuid
     * @param result 返回结果
     * @param opt 可选项
     * @param fail 失败回调
     * @param success 成功回调
     */
    private doCommandFinish(servicename: string, command: number, params: any, auuid: string, result: IResult, opt: IExecuteOption, success: FnPXResponce, fail: FnPXResponce) {
        const callbackRsp: IPXResponce = { servicename, cmd: command, infocmd: command, param: params, uuid: auuid, result, bubble: true };
        if (result.rsp_error < 0) {
            fail(callbackRsp);
        } else {
            success(callbackRsp);
        }
    }
}
