/**
 * 管理音频设备
 */
export class AudioDeviceManager {
    /**
     * 扬声器列表
     */
    private inputList: string[] = [];

    /**
     * 麦克风列表
     */
    private outputList: string[] = [];

    constructor() {
    }

    public updateDevice(deviceInfo: IAudioDeviceInfo): { input: boolean, output: boolean } {

        const changinfo: {input: boolean, output: boolean} = {input: false, output: false};

        if (deviceInfo.audio_input) {
            const inputChange = this.checkStringArrayChange(this.inputList, deviceInfo.audio_input_list);
            if (inputChange.add.length > 0 || inputChange.remove.length > 0) {
                console.error("audio input 有改动", inputChange);
                changinfo.input = true;
            }
            this.inputList = deviceInfo.audio_input_list;
        } else {
            if (this.inputList.length > 0) {
                console.error("audio input 移除", `${this.inputList}`);
                changinfo.input = true;
                this.inputList.length = 0;
            }
        }

        if (deviceInfo.audio_output) {
            const outputChange = this.checkStringArrayChange(this.outputList, deviceInfo.audio_output_list);
            if (outputChange.add.length > 0 || outputChange.remove.length > 0) {
                console.error("audio output 有改动", outputChange);
                changinfo.output = true;
            }
            this.outputList = deviceInfo.audio_output_list;
        } else {
            if (this.outputList.length > 0) {
                console.error("audio output 移除", `${this.outputList}`);
                changinfo.output =  true;
                this.outputList.length = 0;
            }
        }

        return changinfo;
    }

    /**
     * 更新输出设备列表
     * @param outputList 音频输出设备列表
     */
    public setOutputList( outputList: string[]): void {
        this.outputList = outputList;
    }

    /**
     * 更新输入设备列表
     * @param inputList 音频输入设备列表
     */
    public setInputList(inputList: string[]): void {
        this.inputList = inputList;
    }

    /**
     * 检测两个string数组的区别
     * @param oldArray 原始数组
     * @param newArray 新数组
     */
    private checkStringArrayChange(oldArray: string[], newArray: string[]): { add: string[], remove: string[] } {
        const changeInfo: { add: string[], remove: string[] } = { add: [], remove: [] };
        oldArray.forEach((old) => {
            if (newArray.indexOf(old) < 0) {
                changeInfo.remove.push(old);
            }
        });

        newArray.forEach((newone) => {
            if (oldArray.indexOf(newone) < 0) {
                changeInfo.add.push(newone);
            }
        });
        // todo: 检测两个string数组的区别
        return changeInfo;
    }
}
