export class RegExpHelper {

    /**
     * 解析ini文件
     * @param data
     */
    public static parseINIString(data: string): any {
        const regex = {
            section: /^\s*\s*([^]*)\s*\]\s*$/,
            param: /^\s*([\w\.\-\_]+)\s*=\s*(.*?)\s*$/,
            comment: /\s*;.*$/,
        };
        const value: any = {};
        const lines = data.split(/\r\n|\r|\n/);
        let section: any = null;
        lines.forEach((line) => {
            if (regex.comment.test(line)) {
                const match = line.match(regex.comment);
                if (match.index === 0) {
                    return;
                } else {
                    line = line.substr(0, match.index);
                }
            }
            if (regex.param.test(line)) {
                const match = line.match(regex.param);
                if (section) {
                    value[section][match[1]] = match[2];
                } else {
                    value[match[1]] = match[2];
                }
            } else if (regex.section.test(line)) {
                const match = line.match(regex.section);
                section = match[1].substr(1);
                value[section] = {};
            }
        });
        return value;
    }

    /**
     * 判断data是否是文件路径
     * @param data
     */
    public static isFullFilePath(data: string): boolean {
        const filePathRegx = /^(file:\/)?\b[a-zA-Z]:(\/\w+)+.\w+/;
        return filePathRegx.test(data);
    }

    /**
     * 从data中获取全路径目录
     * @param data 格式为/D:/XXX/XXX/.../XXX.ext
     */
    public static getFullPathDir(data: string): string {
        const dirRegx = /\b[a-zA-Z]:(\/\w+)+\//;
        return data.match(dirRegx)[0];
    }
}
