import { NativeBridge } from "./NativeBridge";
import { BaseCommand } from "../helper/queue/BaseCommand";
import { PromiseCommandQueue } from "../helper/queue/PromiseCommandQueue";
import { ExecuteOption } from "./ExecuteOption";
import { SERVICE_OPEN_STATUS } from "../defs/SERVICE_OPEN_STATUS";
import { CommonError } from "../defs/Basedefs";

/**
 * Service的通用功能
 */
export abstract class BaseService implements IBaseService {

    // public static commandQueue: PromiseCommandQueue = new PromiseCommandQueue();
    private openstatus: SERVICE_OPEN_STATUS = SERVICE_OPEN_STATUS.CLOSED;
    protected aibrowser: IAIBrowser;
    protected bridge: NativeBridge;
    public message: {};
    public error: {};
    /**
     * 设置超时 单位ms
     */
    protected timeout: number = 10000;

    public serviceName: string;
    public isOpen: boolean = false;
    public get openStatus(): SERVICE_OPEN_STATUS {
        return this.openstatus;
    }
    constructor(aibrowser: IAIBrowser, bridge: NativeBridge) {
        this.aibrowser = aibrowser;
        this.bridge = bridge;
        this.isOpen = false;
    }

    /**
     * 开启服务
     */
    public open(timeout?: number): Promise<IPXResponce> {
        if (typeof timeout !== "number") {
            timeout = this.timeout;
        }
        console.log("开启服务", this.serviceName, Date.now());
        return new Promise((onOpenSuccess, onOpenFailure) => {
            this.openstatus = SERVICE_OPEN_STATUS.OPENNING;
            this.aibrowser.openService(this.serviceName, timeout, (e: IPXResponce) => {
                this.isOpen = true;
                this.openstatus = SERVICE_OPEN_STATUS.OPENED;
                onOpenSuccess(e);
            }, (e: IPXResponce) => {
                this.openstatus = SERVICE_OPEN_STATUS.OPEN_ERROR;
                onOpenFailure(e);
                this.aibrowser.onError(e);
            });
        });
    }

    /**
     * 关闭服务
     */
    public close(timeout?: number): Promise<IPXResponce> {
        if (typeof timeout !== "number") {
            timeout = this.timeout;
        }
        return new Promise((onCloseSuccess, onCloseFailure) => {
            this.openstatus = SERVICE_OPEN_STATUS.CLOSING;
            this.aibrowser.closeService(this.serviceName, timeout, (e: IPXResponce) => {
                this.isOpen = false;
                this.openstatus = SERVICE_OPEN_STATUS.CLOSED;
                onCloseSuccess(e);
            }, (e: IPXResponce) => {
                this.openstatus = SERVICE_OPEN_STATUS.CLOSE_ERROR;
                onCloseFailure(e);
                this.aibrowser.onError(e);
            });
        });
    }

    /**
     * 执行服务
     * @param command
     * @param params
     * @param opt
     */
    public execute(command: number, params: any, opt: IExecuteOption): Promise<IPXResponce> {

        if (typeof opt.timeout !== "number") {
            opt.timeout = this.timeout;
        }

        return new Promise((resolve, reject) => {
            this.aibrowser.executeService(this.serviceName, command, params, (uuid: string) => {
                this.bindMessageCallbacks(opt, uuid);
            },
                opt, resolve, reject);
        });
    }

    public getInfo(command: number, params: any, opt: IExecuteOption): Promise<IPXResponce> {
        if (typeof opt.timeout !== "number") {
            opt.timeout = this.timeout;
        }

        return new Promise((resolve, reject) => {
            this.aibrowser.getServiceInfo(this.serviceName, command, params, opt,
                resolve, reject);
        });
    }

    /**
     * 处理execute错误信息
     * @param responce
     */
    public onError(responce: IPXResponce): boolean {
        console.warn(Date.now(), "service处理cmd错误", responce);
        return true;
    }

    /**
     * 处理info错误信息
     * @param responce
     */
    public onInfoError(responce: IPXResponce): boolean {
        console.warn(Date.now(), "service处理info错误", responce);
        return true;
    }

    /**
     * 添加消息回调
     * @param msg
     * @param listener
     */
    public addListener(msg: string | number, listener: IListener): void {
        this.aibrowser.addListener(msg, listener);
    }

    /**
     * 添加一次消息回调,回调执行后自动销毁
     * @param msg
     * @param listener
     */
    public addOnceListener(msg: string | number, listener: IListener): void {
        this.aibrowser.addOnceListener(msg, listener);
    }

    /**
     * 销毁特定消息回调
     * @param msg
     * @param listener
     */
    public removeListener(msg: string | number, listener: IListener): void {
        this.aibrowser.removeListener(msg, listener);
    }

    /**
     * 销毁msg下的所有回调
     * @param msg
     */
    public clearListeners(msg: string | number): void {
        this.aibrowser.clearListeners(msg);
    }

    /**
     * 创建executeOption
     * @param onceMsgbinds 单次消息回调
     * @param msgbinds 多次消息回调
     * @param requestIdCallback 收到requestId的回调
     * @param timeout 超时设置 单位ms 0ms表示没有超时(若不返回则一直等待)
     */
    public makeExecuteOption(
        onceMsgbinds?: { [key: string]: IListener },
        msgbinds?: { [key: string]: IListener },
        requestIdCallback?: (uuid: string) => void,
        timeout?: number): IExecuteOption {
        const option: IExecuteOption = new ExecuteOption();
        if (onceMsgbinds) {
            option.onceMsgBinds = this.obj2Map(onceMsgbinds);
        }
        if (msgbinds) {
            option.msgBinds = this.obj2Map(msgbinds);
        }
        option.requestIdCallback = requestIdCallback;
        option.timeout = timeout;

        return option;
    }

    private obj2Map(obj: { [key: number]: any }): Map<number, any> {
        const map: Map<number, any> = new Map();
        for (const key in obj) {
            if (obj[key]) {
                map.set(parseInt(key, 10), obj[key]);
            }
        }
        return map;
    }

    public makeCommand(type: keyof ICommandType, cmd: number, param: any, option: IExecuteOption): BaseCommand {
        const opt: IExecuteOption = option || new ExecuteOption();
        return new BaseCommand(this, type, cmd, param, opt);
    }

    public makeExecutePromise(cmd: number, param: any, option: IExecuteOption): Promise<IPXResponce> {
        const opt: IExecuteOption = option || new ExecuteOption();
        if (this.openStatus === SERVICE_OPEN_STATUS.OPENED) {
            return this.doMakeExecutePromise(cmd, param, opt);
        } else {
            return this.makeNotOpenResponce(cmd, param, opt);
        }
        // return new Promise((resolve, reject) => {
        //     const command = this.makeCommand(0, cmd, param, option);
        //     command.succeed = (e) => { resolve(e); };
        //     command.fail = (reason) => { reject(reason); };
        //     BaseService.commandQueue.enqueue(command);
        // });
    }

    private makeNotOpenResponce(cmd: number, param: any, option: IExecuteOption) {
        const notOpenResponce: IPXResponce = {
            servicename: this.serviceName,
            uuid: "service not opened",
            cmd,
            param,
            result: {
                rsp_error: CommonError.PX_ERR_CONNECTION_LOST,
            },
            option,
        };
        return Promise.reject(notOpenResponce);
    }

    public makeInfoPromise(cmd: number, param: any, option: IExecuteOption): Promise<any> {
        // return new Promise((resolve, reject) => {
        //     const command = this.makeCommand(1, cmd, param, option);
        //     command.succeed = (e) => { resolve(e); };
        //     command.fail = (reason) => { reject(reason); };
        //     BaseService.commandQueue.enqueue(command);
        // });
        const opt: IExecuteOption = option || new ExecuteOption();
        if (this.openStatus === SERVICE_OPEN_STATUS.OPENED) {
            return this.doMakeInfoPromise(cmd, param, opt);
        } else {
            return this.makeNotOpenResponce(cmd, param, opt);
        }
    }

    public doMakeExecutePromise(cmd: number, param: any, option: IExecuteOption): Promise<IPXResponce> {

        // return new Promise((resolve, reject) => {
        return this.execute(cmd, param, option).then((e: IPXResponce) => {
            return Promise.resolve(e);
        }).catch((e: IPXResponce) => {
            delete e.infocmd;
            if (typeof option.onError === "function") {
                option.onError(e); // app处理
            }
            if (e.bubble === true) {
                this.onError(e);
            }
            if (e.bubble === true) {
                this.aibrowser.onError(e); // aibrowser处理
            }
            return Promise.reject(e);
        });
    }

    public doMakeInfoPromise(cmd: number, param: any, option: IExecuteOption): Promise<any> {
        // return new Promise((resolve, reject) => {
        return this.getInfo(cmd, param, option).then((a: IPXResponce) => {
            if (a && a.result && a.result.rsp_data) {
                if (a.result.rsp_data.data) {
                    console.log("info returns:", a, a.result.rsp_data);
                    return Promise.resolve(a.result.rsp_data.data);
                } else {
                    console.log("info returns:", a, a.result.rsp_data);
                    return Promise.resolve(a.result.rsp_data);
                }
            } else {
                throw new Error("Info接口返回数据错误 " + cmd);
            }
        }).catch((e: IPXResponce) => {
            delete e.cmd;
            if (typeof option.onError === "function") {
                option.onError(e); // app处理
            }
            if (e.bubble === true) {
                this.onInfoError(e);
            }
            if (e.bubble === true) {
                this.aibrowser.onError(e); // aibrowser处理
            }
            return Promise.reject(e);
        });
        // });
    }

    public setDefaultTimeout(timeout: number): void {
        this.timeout = timeout;
    }
    private bindMessageCallbacks(opt: IExecuteOption, uuid: string) {
        if (opt.msgBinds) {
            opt.msgBinds.forEach((value, key) => {
                const token = uuid + "_" + key; ///
                this.addListener(token, value);
            });
        }
        if (opt.onceMsgBinds) {
            opt.onceMsgBinds.forEach((value, key) => {
                const token = uuid + "_" + key; ///
                this.addOnceListener(token, value);
            });
        }
    }

    private unbindMessageCallbacks(opt: IExecuteOption, uuid: string) {
        if (opt.msgBinds) {
            opt.msgBinds.forEach((listener, key) => {
                const token = uuid + "_" + key; ///
                this.removeListener(token, listener);
            });
        }
    }
}
