export class AnswerToolBindings {
    /**
     * 缓存下来的绑定信息 返回绑定成功消息后，会根据学生id将缓存元素删除
     */
    private bufferredStudentInfo: Map<string, IAnswerBindRequest> = new Map();
    /**
     * 返回绑定成功消息后，将学生信息保存在list中
     */
    private bindedStudentInfoList: Map<string, IAnswerBindResult> = new Map();

    /**
     * 缓存的绑定请求 请求已发出但还未返回成功消息
     */
    public get bufferredRequest(): IAnswerBindRequest[] {
        return Array.from(this.bufferredStudentInfo.values());
    }

    /**
     * 绑定的学生信息
     */
    public get bindedStudents(): IAnswerBindResult[] {
        return Array.from(this.bindedStudentInfoList.values());
    }

    /**
     * 添加新的绑定请求
     * @param studentsInfo
     */
    public pushNewRequest(studentsInfo: IAnswerBindRequest[]): void {
        studentsInfo.forEach((stdInfo) => {
            if (this.bufferredStudentInfo.has(stdInfo.id)) {
                this.bufferredStudentInfo.delete(stdInfo.id);
            }
            this.bufferredStudentInfo.set(stdInfo.id, stdInfo);
        });
    }

    /**
     * 添加新的绑定成功信息
     * @param bindInfo
     */
    public addBindedInfo(bindInfo: IAnswerBindResult): void {
        if (this.bindedStudentInfoList.has(bindInfo.user_id)) {
            throw new Error("重复绑定相同的学生id" + JSON.stringify(bindInfo) + JSON.stringify(this.bindedStudentInfoList.get(bindInfo.user_id)));
        }
        this.bindedStudentInfoList.set(bindInfo.user_id, bindInfo);
        this.bufferredStudentInfo.delete(bindInfo.user_id);
    }

    /**
     * 删除绑定信息
     * @param stuId
     */
    public removeBindInfo(stuId: string): void {
        this.bindedStudentInfoList.delete(stuId);
    }
}
