/**
 * 有些对象不一定会立即返回，BufferedQuery负责
 * 将请求的命令缓存起来，当对象准备好后再调用命令
 */
export class BufferedQuery {
    /**
     * 访问的目标
     */
    private target: any;
    private state: QueryState = QueryState.NotStart;
    private buffer: Array<() => void> = [];
    private retrieve: () => Promise<any>;

    constructor(retrieve: () => Promise<any>) {
        this.retrieve = retrieve;
    }

    public getTarget(): any {
        return this.target;
    }

    public doQuery(cb: () => void): void {
        if (this.state === QueryState.IsReady) {
            cb();
        } else {
            this.buffer.push(cb);
        }
        if (this.state === QueryState.NotStart) {
            this.state = QueryState.IsRetrieving;
            this.retrieve().then((target) => {
                this.target = target;
                this.state = QueryState.IsReady;
                this.buffer.forEach((cb2) => {
                    cb2();
                });
                this.buffer.length = 0;
            });
        }
    }

}
/**
 * 被请求对象的状态
 */
enum QueryState {
    IsReady = 0, // 可用
    IsRetrieving, // 正在获取
    NotStart, // 不可用
}
