import { NativeBridge } from "./NativeBridge";
import { CameraService } from "../services/CameraService";
import { AIService } from "../services/AIService";
import { ServiceManager } from "../services/ServiceManager";
import { AnswerToolService } from "../services/AnswerToolService";
import { LocalFileService } from "../services/LocalFileService";
import { PlayerService } from "../services/PlayerService";
import { UnityService } from "../services/UnityService";
import { SystemService } from "../services/SystemService";
import { PXVideoProcessorService } from "../services/PXVideoProcessorService";
import { VideoCaptureService } from "../services/VideoCaptureService";

export type ServiceType = new (aibrowser: IAIBrowser, bridge: NativeBridge) => IBaseService;
declare global {
    interface IServiceMeta {
        name: string; // 服务的逻辑名称,与service的serviceName一致
        prefix: number;
        class: ServiceType;
        waitForOpen: boolean;
        timeout: number;
    }
}
export class ServiceLocator {
    private serviceMetaList: IServiceMeta[] = [
        {
            name: "Camera",
            prefix: 3,
            class: CameraService,
            waitForOpen: true,
            timeout: 10000,
        },
        {
            name: "AIService",
            prefix: 1,
            class: AIService,
            waitForOpen: false,
            timeout: 50000,
        },
        {
            name: "ServiceManager",
            prefix: 20,
            class: ServiceManager,
            waitForOpen: true,
            timeout: 10000,
        },
        {
            name: "PxAnswerService",
            prefix: 4,
            class: AnswerToolService,
            waitForOpen: false,
            timeout: 20000,
        },
        {
            name: "LocalFileService",
            prefix: 5,
            class: LocalFileService,
            waitForOpen: true,
            timeout: 10000,
        },
        {
            name: "PlayerService",
            prefix: 2,
            class: PlayerService,
            waitForOpen: true,
            timeout: 10000,
        },
        {
            name: "Unity",
            prefix: 30,
            class: UnityService,
            waitForOpen: true,
            timeout: 10000,
        },
        {
            name: "Sys",
            prefix: 6,
            class: SystemService,
            waitForOpen: true,
            timeout: 10000,
        },
        {
            name: "PxVideoProcessor",
            prefix: 7,
            class: PXVideoProcessorService,
            waitForOpen: false,
            timeout: 20000,
        },
        {
            name: "videocapture",
            prefix: 8,
            class: VideoCaptureService,
            waitForOpen: false,
            timeout: 20000,
        },
    ];
    private namedMap: Map<string, IServiceMeta> = new Map();
    private prefix2NameMap: Map<number, string> = new Map();
    private class2NameMap: Map<ServiceType, string> = new Map();
    public runningServices: Map<string, IBaseService> = new Map();
    private aibrowser: IAIBrowser;
    private bridge: NativeBridge;

    constructor(aibrowser: IAIBrowser, bridge: NativeBridge) {
        this.aibrowser = aibrowser;
        this.bridge = bridge;
        this.serviceMetaList.forEach((meta) => {
            this.namedMap.set(meta.name, meta);
            this.prefix2NameMap.set(meta.prefix, meta.name);
            this.class2NameMap.set(meta.class, meta.name);
        });
    }

    public getServiceByName(name: string): IBaseService {
        let service = this.runningServices.get(name);
        if (service === undefined) {
            const meta = this.namedMap.get(name);
            if (meta === undefined) {
                throw new Error(`${name} 服务 不存在!`);
            }
            service = new meta.class(this.aibrowser, this.bridge);
            service.setDefaultTimeout(meta.timeout);
            this.runningServices.set(name, service);
        }
        return service;
    }

    public getServiceByPrefix(prefix: number): IBaseService {
        const name = this.prefix2NameMap.get(prefix);
        if (name === undefined) {
            throw new Error(`前缀为${prefix} 的服务不存在!`);
        }
        return this.getServiceByName(name);
    }

    public getServiceByClass(classType: ServiceType): IBaseService {
        const name = this.class2NameMap.get(classType);
        if (name === undefined) {
            throw new Error(`${classType} 不存在!`);
        }
        return this.getServiceByName(name);
    }

    public getAllServiceNames(): string[] {
        const list: string[] = [];
        this.serviceMetaList.forEach((meta) => {
            list.push(meta.name);
        });
        return list;
    }

    /**
     * 获取名称为name的服务的meta信息
     * @param name
     */
    public getMetaInfo(name: string): IServiceMeta {
        let ret: IServiceMeta = null;
        this.serviceMetaList.forEach((meta) => {
            if (meta.name === name) {
                ret = meta;
            }
        });
        return ret;
    }
}
