import { BaseService } from "../frame/BaseService";
import { NativeBridge } from "../frame/NativeBridge";
import { ExtendedBridge } from "../ExtendedBridge";
import { AIService } from "./AIService";
import { Log } from "../helper/log/Log";
import { Flow } from "../helper/Flow";

interface IBonesCollection {
    functionGameResult: IBoneResult[];
}
interface IBoneResult {
    bones: Array<{ x: number, y: number }>;
    stuID: string;
    id: string;
}
interface IPlayerArea {
    functionGameShowRegion: Array<{ height: number, stuID: string, width: number, x: number, y: number }>;
}

declare global {
    // tslint:disable-next-line: interface-name
    interface Window {
        courseGlobalScheduler: any;
    }
}

/**
 * 游戏侧发过来的消息
 */
enum UnityMessages {
    UPDATE_VERTICES = "update_vertices", // 更新顶点数据
    UPDATE_VP = "update_vp", // 更新MVP矩阵
    CHANGE_POSE = "change_pose", // 开始检测人物的骨骼点
    START_SECTION = "start_section", // 某一环节开始
    PLAYER_SELECTED = "player_selected", // 选中的学生
    PLAYER_RECOGNITION_STATE = "player_recognition_state", // 学生识别状态
    START_QUESTION = "start_question", // 开始答题
    STOP_QUESTION = "stop_question", // 停止答题
    QUERY_TOTAL_POINTS = "query_total_points", // 查询学期成绩
    GAME_LOADED = "game_loaded", // 游戏加载完毕
    GAME_ENDED = "game_ended", // 游戏结束
}

/**
 * 发送给Unity的消息
 */
enum ToUnityMessages {
    CHANGE_PLAYER = "change_player", // 更换学生
    GO_NEXT = "go_next", // 进入下一个游戏环节
    QUIT_GAME = "quit_game", // 强制结束游戏
    HEAD_GALLERY = "head_gallery", // 发送学生头像
    PLAYER_AREA = "player_area", // 设置学生区域
    CLASS_INFO = "class_info", // 发送学生信息
    KEY_DOWN = "key_down", // 按键消息
    TOTAL_POINTS = "total_points", // 发送学期成绩
    BONES = "bones", // 发送骨骼点信息
}

export class UnityService extends BaseService {

    private poseName: string = "";
    private lastPoseName: string = "";
    private unityMessageCallbackMap: Array<{ msg: string, callback: (args: any) => void }> = [];
    private get exBridge(): ExtendedBridge {
        return this.bridge as ExtendedBridge;
    }
    constructor(aibrowser: IAIBrowser, bridge: NativeBridge) {
        super(aibrowser, bridge);
        this.serviceName = "UnityService";
        this.registerUnityMessages();
    }

    public open(): Promise<IPXResponce> {
        return new Promise((resolve) => {
            this.isOpen = true;
            resolve({ servicename: "unity", uuid: "", result: null });
        });
    }

    public close(): Promise<IPXResponce> {
        return new Promise((resolve) => {
            this.isOpen = false;
            resolve({ servicename: "unity", uuid: "", result: null });
        });
    }

    /**
     * 注册来自Unity的消息
     * 这些消息一般是互动组件定义好的消息
     */
    public registerUnityMessages(): void {

        this.unityMessageCallbackMap = [
            {
                msg: "update_vertices",
                callback: (args) => {
                    args.name = "Camera";
                    this.exBridge.updateVertices(args);
                },
            },
            {
                msg: "update_vp",
                callback: (args) => {
                    this.exBridge.updateVPMatrix(args);
                },
            },
            {
                msg: "stop_pose",
                callback: () => {
                    this.exBridge.stopPoseDetect();
                },
            },
            {
                msg: "change_pose",
                callback: (ids: string[]) => {
                    Log.log("开启骨骼点检测", ids);
                    // 开始检测
                    Log.log("开始检测pose");
                    const idList: any[] = [];
                    ids.forEach((id) => {
                        idList.push({
                            stuID: id,
                        });
                    });
                    const param = { stuList: idList };
                    (this.exBridge.getServiceByName("AIService") as AIService).px_cmd_ai_stop_concerntration();
                    this.exBridge.changePose(param, (eventInfo: IBonesCollection) => {
                        // console.log(eventInfo.functionGameResult);
                        eventInfo.functionGameResult.forEach((bones) => {
                            bones.id = bones.stuID;
                            this.transferToUnity("bones", bones);
                        });
                    }, (eventInfo: IPlayerArea) => {
                        Flow.waitForSeconds(3).then(() => {
                            eventInfo.functionGameShowRegion.forEach((region) => {
                                this.transferToUnity("player_area",
                                    {
                                        id: region.stuID,
                                        left: region.x,
                                        top: region.y,
                                        width: region.width,
                                        height: region.height,
                                    });
                            });
                        });

                    });
                },
            },
            {
                msg: "bind_answerTool",
                callback: (args) => {
                    console.warn("绑定答题器", args);
                    this.exBridge.loadStudentsInfor(args, 0);
                },
            },
            {
                msg: "start_question",
                callback: (args) => {
                    console.warn("开启答题器~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~\n\n\n\n");
                    this.exBridge.startAnswerDevice(8, (id: string, key: string ) => {
                        Log.log("按键", id, key);
                        this.transferToUnity("key_down", {id, keycode: key});
                    });
                },
            },
            {
                msg: "stop_question",
                callback: (args) => {
                    console.warn("停止答题器~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~\n\n\n\n");
                    this.exBridge.stopAnswerDevice(8, "0");
                },
            },
            {
                msg: "query_total_points",
                callback: (args) => {
                },
            },
            {
                msg: "game_loaded",
                callback: (args) => {
                    console.log("%c游戏加载完毕", "background:#00f0f0", args);

                    const galleries: IHeaderShowBindData[] = [];
                    const students: Array<{
                        id: string,
                        name: string,
                        gid: string,
                    }> = [];
                    window.courseGlobalScheduler.store.classInfo.internalStuMap.forEach((stuInfo: any) => {
                        const stu: {stuID: string, stuName: string, gender: number, MRID: number} = stuInfo.student;
                        const avatar = stuInfo.avator;
                        students.push({
                            id: stu.stuID,
                            name: stu.stuName,
                            gid: stu.MRID === undefined ? "" : `${stu.MRID}`,
                        });

                        if (avatar !== null && avatar !== undefined) {
                            galleries.push({
                                gid: avatar.id,
                                stuID: stu.stuID,
                                showImage: avatar.headimg,
                                x: avatar.x,
                                y: avatar.y,
                                width: avatar.width,
                                height: avatar.height,
                            });
                        }

                    });
                    this.transferToUnity("class_info", { students });
                    this.transferToUnity("head_gallery", { headers: galleries });
                    // window.mocker.sendClassInfo(); // 发送学生信息 没有gid信息
                    // const stuList: any[] = [];
                    // for (let i = 0; i < 2; ++i) {
                    //     stuList.push({
                    //         id: window.mocker.ids[i],
                    //         row1: window.mocker.ids[i],
                    //         row3: window.mocker.names[i],
                    //     });
                    // }

                    const stuList: Array<{id: string, row1?: string, row2?: string, row3?: string}> = [];
                    students.forEach((stu) => {
                        stuList.push({
                            id: stu.id,
                            row1: stu.id,
                            row3: stu.name,
                        });
                    });
                    // this.exBridge.createAnswer("PX", 9).then((e) => {
                    //     return this.exBridge.loadStudentsInfor(stuList, 0);
                    // });
                    this.exBridge.setFullScreen(true);
                    this.transferToUnity("hide_background", "");
                    Flow.waitForSeconds(1).then(() => {
                        return (this.exBridge.getServiceByName("AIService") as AIService).px_cmd_ai_start_concerntration();
                    });
                    // this.exBridge.checkoutStudentHead().then((result) => {
                    //     let i = 0;
                    //     const bindInfo: IHeaderBindData[] = [];
                    //     const showBindInfo: IHeaderShowBindData[] = [];
                    //     result.galleries.forEach((gallery) => {
                    //         bindInfo.push({
                    //             stuID: window.mocker.ids[i], // 绑定
                    //             faceImg: gallery.faceImg,
                    //             gid: gallery.gid,
                    //         });
                    //         showBindInfo.push(
                    //             {
                    //                 gid: gallery.gid,
                    //                 stuID: window.mocker.ids[i], // 绑定
                    //                 showImage: gallery.showImg,
                    //                 x: gallery.x,
                    //                 y: gallery.y,
                    //                 width: gallery.width,
                    //                 height: gallery.height,
                    //             },
                    //         );
                    //         i++;
                    //     });
                    //     this.transferToUnity("head_gallery", { headers: showBindInfo }); // 发送头像绑定信息
                    //     return this.exBridge.bindGallerys({ galleries: bindInfo }); // 通知AIService绑定头像
                    // }).then(() => {
                    //     return Flow.waitForSeconds(1).then(() => {
                    //         return (this.exBridge.getServiceByName("AIService") as AIService).px_cmd_ai_start_concerntration();
                    //     });
                    // });
                },
            },
            {
                msg: "game_ended",
                callback: (args) => {
                    this.exBridge.release();
                },
            },
        ];

        this.unityMessageCallbackMap.forEach((msgHandler) => {
            this.registerMessageCallback(msgHandler.msg, msgHandler.callback);
        });
    }

    public registerMessageCallback(cmd: string, callback: (args: any) => void) {
        window.messageConsumer.registerCallback(cmd, callback, this);
    }

    private poseDetectOn(): boolean {
        return this.lastPoseName !== "";
    }

    /**
     * 向Unity发送消息
     * @param cmd
     * @param eventInfo
     */
    public transferToUnity(cmd: string, eventInfo: any) {
        const message = {
            cmd,
            data: eventInfo,
        };

        window.messager.talkToUnity(JSON.stringify(message));
    }
}
