export class WindowMessageTransfer {
    public hasFrame: boolean = false;
    private unityFrame: HTMLIFrameElement;
    constructor(hasFrame: boolean) {
        this.unityFrame = document.getElementById("unityframe") as HTMLIFrameElement;
        this.hasFrame = (this.unityFrame !== null);
        window.addEventListener("message", this.receiveMessage, false);
    }
    public talkToUnity(msg: string) {
        if (this.hasFrame) {
            // 将消息转发给Unity的iFrame
            this.unityFrame.contentWindow.postMessage(msg, "*");
        } else {
            window.gameInstance.SendMessage("JSBridge", "onMessage", msg);
        }
    }
    private receiveMessage(evt: MessageEvent): void {
        window.messageConsumer.execute(evt.data);
    }
}
