import { AnswerToolError, AnswerToolExecuteCommand, AnswerToolInfoCommand, AnswerToolMessage } from "../defs/AnswerTooldefs";
import { CameraError, CameraExecuteCommand, CameraInfoCommand, CameraMessage } from "../defs/Cameradefs";
import { ServiceManagerError, ServiceManagerExecuteCommand, ServiceManagerInfoCommand, ServiceManagerMessage } from "../defs/ServiceManagerdefs";
import { PlayerError, PlayerExecuteCommand, PlayerInfoCommand, PlayerMessage } from "../defs/Playerdefs";
