declare global {
    export interface IServiceMetaData {
        /**
         * 开启服务时传递给AIBrowser的名称
         */
        logicalName: string;
        /**
         * 类名
         */
        aliasName: string;
        /**
         * enum前缀
         */
        shortName: string;
        /**
         * c++的常量定义文件名
         */
        defFileName: string;
        /**
         * 生成的enum定义文件名
         */
        targetDefName: string;
        errorCodeType?: any;
        executeCommandType?: any;
        infoCommandType?: any;
        messageCodeType?: any;
        /**
         * 超时
         */
        timeout?: number;
        /**
         * 是否等待服务开启
         */
        waitForOpen?: boolean;
    }
}

const serviceDefs: Map<string, IServiceMetaData> = new Map<string, IServiceMetaData>();

serviceDefs.set("AIService", {
    logicalName: "AIService",
    aliasName: "AIService",
    shortName: "AI",
    defFileName: "Services/aiservice/AIServiceDef.h",
    targetDefName: "AIdefs.ts",
    errorCodeType: AIError,
    executeCommandType: AIExecuteCommand,
    infoCommandType: AIInfoCommand,
    messageCodeType: AIMessage,
    waitForOpen: false,
    timeout: 50000,
});
