import { PlayerMessage } from "../../defs/Playerdefs";
import { AnswerToolMessage } from "../../defs/AnswerTooldefs";
import { CameraMessage } from "../../defs/Cameradefs";
import { SystemMessage } from "../../defs/Systemdefs";
import { NativeBridge } from "../../frame/NativeBridge";
import { ExtendedBridge } from "../../ExtendedBridge";
import { CLIENT_MESSAGE } from "./CLIENT_MESSAGE";

/**
 * 发送消息
 */
export class Messager {

    constructor(private bridge: NativeBridge) { }
    private callbacks: Array<{ callback: (...args: any[]) => void, context: any }> = [];
    private inited: boolean = false;

    /**
     * 添加消息回调
     * 回调的参数为 {message: {cmd: string, [key: string]:any}}
     * @param callback
     * @param context
     */
    public attachMessageObserver(callback: (...args: any[]) => void, context: any = null) {
        // todo: 向bridge注册回调的过程与该方法分离出来，显式调用一次
        if (this.inited === false) { // 第一次时注册aibrowser的消息 之前用回调列表长度来判断，如果将所有的回调detach了，会导致aibrowser的messageCallback注册两次这一回调
            this.inited = true;
            this.bridge.addServiceMessageCallback(PlayerMessage.PX_MSG_PLAYER_TIMEUPDATE, {
                handler: (msg, eventInfo, type, requestid) => {
                    const message = {
                        cmd: CLIENT_MESSAGE.TIME_UPDATE,
                        videoTime: eventInfo.currentTime,
                        key: eventInfo.key,
                        pause: false,
                    };
                    this.emitServiceMessage(message);
                },
            });

            this.bridge.addServiceMessageCallback(PlayerMessage.PX_MSG_PLAYER_STOP, {
                handler: (msg, eventInfo, type, requestId) => {
                    const message = {
                        cmd: CLIENT_MESSAGE.CURRENT_COURSE_END,
                        videoTime: eventInfo.currentTime,
                        key: eventInfo.key,
                        pause: false,
                    };
                    this.emitServiceMessage(message);
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(PlayerMessage.PX_MSG_PLAYER_INTERRUPT, {
                handler: (msg, eventInfo, type, requestId) => {
                    const message = {
                        cmd: CLIENT_MESSAGE.CURRENT_COURSE_INTERRUPT,
                        key: eventInfo.key,
                    };
                    this.emitServiceMessage(message);
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(AnswerToolMessage.PX_MSG_ANSWER_BIND, {
                handler: (msg, eventInfo, type, requestId) => {
                    const message = {
                        cmd: CLIENT_MESSAGE.ANSWER_TOOL_BIND_STATE,
                        deviceId: eventInfo.device_id,
                        studentId: eventInfo.user_id,
                        action: 1,
                    };
                    this.emitServiceMessage(message);
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(AnswerToolMessage.PX_MSG_ANSWER_INSERT, {
                handler: (msg, eventInfo, type, requestId) => {
                    const message = {
                        cmd: CLIENT_MESSAGE.ANSWER_TOOL_BIND_STATE,
                        deviceId: eventInfo.device_id,
                        studentId: 0,
                        action: 0,
                    };
                    this.emitServiceMessage(message);
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(AnswerToolMessage.PX_MSG_ANSWER_USB, {
                handler: (msg, eventInfo, type, requestId) => {
                    let status = 0; // 0:插入电源 1:电源拔出
                    if (eventInfo === "insert") {
                        status = 0;
                    } else if (eventInfo === "pull") {
                        status = 1;
                    } else {
                        throw new Error("答题器USB插拔消息格式不对: \n" + JSON.stringify(eventInfo));
                    }
                    const message = {
                        cmd: CLIENT_MESSAGE.ANSWER_TOOL_USB_CHANGE,
                        status,
                    };
                    this.emitServiceMessage(message);
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(AnswerToolMessage.PX_MSG_ANSWER_INIT, {
                handler: (msg, eventInfo, type, requestId) => {
                    const message = {
                        cmd: CLIENT_MESSAGE.ANSWER_TOOL_INIT,
                        init: eventInfo.init,
                        reason: eventInfo.reason,
                        answertype: eventInfo.answertype,
                    };
                    this.emitServiceMessage(message);
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(CameraMessage.PX_MSG_CAMERA_STATUSCHANGED, {
                handler: (msg, eventInfo, type, requestId) => {
                    // todo: 通知cameraChange消息
                    (this.bridge as ExtendedBridge).checkCamera().then((res) => {
                        const devices: Array<{ id: string, name: string }> = [];
                        res.devices.forEach((dev) => {
                            devices.push(dev);
                        });
                        const message = {
                            cmd: CLIENT_MESSAGE.CAMERA_CHANGE,
                            devices,
                        };
                        this.emitServiceMessage(message);
                    });
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(SystemMessage.PX_MSG_SYS_AUDIOCHANGED, {
                handler: (msg, eventInfo, type, requesId) => {
                    const change = this.bridge.audioDeviceManager.updateDevice(eventInfo);
                    if (change.input) {
                        this.emitServiceMessage({ cmd: CLIENT_MESSAGE.MICROPHONE_CHANGE });
                    }
                    if (change.output) {
                        this.emitServiceMessage({ cmd: CLIENT_MESSAGE.SPEAKER_CHANGE });
                    }
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(SystemMessage.PX_MSG_SYS_AUDIOLEVEL, {
                handler: (msg, eventInfo, type, requestId) => {
                    this.emitServiceMessage({
                        cmd: CLIENT_MESSAGE.VOLUME_EVENT,
                        volume: eventInfo.level,
                    });
                },
                context: this,
            });
            this.bridge.addServiceMessageCallback(SystemMessage.PX_MSG_SYS_HOST_STATUS_CHANGED, {
                handler: (msg, eventInfo, type, requestId) => {
                    this.emitServiceMessage({
                        cmd: CLIENT_MESSAGE.HOST_CONNECTION_CHANGE,
                        data: eventInfo,
                    });
                },
            });
        }

        this.callbacks.push({ callback, context });
    }

    /**
     * 注销消息回调
     * @param callback
     * @param context
     */
    public detachMessageObserver(callback: (...args: any[]) => void, context: any = null): void {

        for (let i = this.callbacks.length - 1; i >= 0; --i) {
            const item = this.callbacks[i];
            if (item.callback === callback) {
                if (context === item.context) {
                    this.callbacks.splice(i, 1);
                }
            }
        }
    }

    public sendMessage(msg: { cmd: string, data: any }) {
        this.emitServiceMessage(msg);
    }

    /**
     * 触发服务消息回调 服务消息是服务主动推送的消息
     * @param message 消息
     */
    public emitServiceMessage(message: { cmd: string, [key: string]: any }) {
        for (let i = 0, len = this.callbacks.length; i < len; ++i) {
            const item = this.callbacks[i];
            item.callback.call(item.context, { message });
        }
    }

}
