
import { AudioDeviceManager } from "../helper/device/AudioDeviceManager";
import { PXFileManager } from "../helper/file/FileManager";
import { Log } from "../helper/log/Log";
import { Messager } from "../helper/message/Messager";
import { LocalFileService } from "../services/LocalFileService";
import { ServiceLocator, ServiceType } from "./ServiceLocator";
import { CodeInterpreter } from "../meta/CodeInterpretor";

export class NativeBridge {
    protected aibrowser: IAIBrowser;
    public serviceLocator: ServiceLocator;
    /**
     * 负责将执行码、查询码、消息码、错误码转化为可读的字符串
     */
    public codeInterpreter: CodeInterpreter;
    protected fileManager: PXFileManager;
    public audioDeviceManager: AudioDeviceManager;
    private messager: Messager;

    constructor(aibrowser: IAIBrowser) {
        this.aibrowser = aibrowser;
        this.serviceLocator = new ServiceLocator(this.aibrowser, this);
        this.codeInterpreter = new CodeInterpreter();
        this.catchUnhandledError();
    }

    private catchUnhandledError() {
        window.onerror = (event, source, lineno, colno, error) => {
            const errInfo = `uncaught error: ${event} \n  url: ${source}:${lineno}:${colno}`;
            console.error(errInfo);
        };

        window.onunhandledrejection = (ev) => {
            if (ev && ev.reason) {
                const errorInfo = `uncaught Promise error: ${ev.reason.message}\n stack:${ev.reason.stack}`;
                console.error(errorInfo);
            } else {
                console.error(`uncaught Promise error: ${JSON.stringify(ev)}`);
            }
        };
    }

    public init(): Promise<any> {
        return this.aibrowser.init();
    }

    public release(): Promise<any> {
        const promiseArray: Array<Promise<any>> = [];

        this.serviceLocator.runningServices.forEach((service, key, map) => {
            promiseArray.push(
                service.close().then((e: any) => { Log.log(key, "服务关闭"); }).catch((e) => { Log.warn(service.serviceName, "服务关闭失败", e); }),
            );
        });

        this.fileManager.release();
        this.clearServiceReadyCallbacks();

        return Promise.all(promiseArray).then(() => {
            Log.log("释放完成");
            this.serviceLocator.runningServices.clear();
            this.aibrowser.release();
        }).catch((reason) => {
            console.error(reason);
        });
    }

    /**
     * 启动servicelist中包含的服务
     * @param servicelist 要启动的服务列表
     */
    public start(servicelist?: string[]): Promise<any> {
        servicelist = servicelist || this.serviceLocator.getAllServiceNames();

        const promiseArray: Array<Promise<any>> = [];

        servicelist.forEach((servicename, index, array) => {
            const service = this.getServiceByName(servicename);
            const meta = this.serviceLocator.getMetaInfo(servicename);
            if (meta.waitForOpen) {
                promiseArray.push(this.openService(service, servicename));
            } else {
                this.openService(service, servicename);
            }
        });

        return new Promise((resolve, reject) => {
            Promise.all(promiseArray).then((a) => {
                Log.log("初始化完成");
                this.fileManager = new PXFileManager(this.getService(LocalFileService));
                this.audioDeviceManager = new AudioDeviceManager();
                this.messager = new Messager(this);
                resolve();
            }).catch((a) => {
                Log.warn("初始化失败", a);
                reject(a);
            });
        });

    }

    /**
     * 停止servicelist中包含的服务
     * @param servicelist 要启动的服务列表
     */
    public stop(servicelist?: string[]): Promise<any> {
        servicelist = servicelist || this.serviceLocator.getAllServiceNames();

        const promiseArray: Array<Promise<any>> = [];

        servicelist.forEach((servicename, index, array) => {
            const service = this.getServiceByName(servicename);
            promiseArray.push(this.stopService(service, servicename));
        });

        return new Promise((resolve, reject) => {
            Promise.all(promiseArray).then((a) => {
                Log.log("关闭完成");
                resolve();
            }).catch((a) => {
                Log.warn("关闭失败", a);
                reject(a);
            });
        });

    }

    ///////// 服务启动完成的回调
    private serviceReadyCallbacks: Array<(servicename: string) => void> = [];
    private openService(service: IBaseService, servicename: string): Promise<any> {
        return service.open().then((responce) => {
            Log.log(servicename, "打开成功", responce, Date.now());
            this.emitServiceReadyEvents(servicename);
        }).catch((responce) => {
            // todo: 失败处理
            Log.log(servicename, "打开失败", responce);
        });
    }

    ///////// 服务停止完成的回调
    private stopService(service: IBaseService, servicename: string): Promise<any> {
        return service.close().then((responce) => {
            Log.log(servicename, "关闭成功", responce, Date.now());
            this.emitServiceReadyEvents(servicename);
        }).catch((responce) => {
            // todo: 失败处理
            Log.log(servicename, "关闭失败", responce);
        });
    }

    /**
     * 注册服务Ready的回调
     * @param callback (servicename: string) => void
     */
    public addServiceReadyCallback(callback: (servicename: string) => void): void {
        this.serviceReadyCallbacks.push(callback);
    }

    /**
     * 销毁服务Ready的回调
     * @param callback (servicename: string) => void
     */
    public removeServiceReadyCallback(callback: (servicename: string) => void): void {
        const index = this.serviceReadyCallbacks.indexOf(callback);
        if (index >= 0) {
            this.serviceReadyCallbacks.splice(index, 1);
        }
    }

    /**
     * 清空服务Ready的回调
     */
    public clearServiceReadyCallbacks(): void {
        this.serviceReadyCallbacks.length = 0;
    }

    /**
     * 触发服务启动成功的回调
     * @param servicename 服务名称
     */
    public emitServiceReadyEvents(servicename: string): void {
        this.serviceReadyCallbacks.forEach((cb) => {
            cb(servicename);
        });
    }
    /////////

    // public get commandQueue(): PromiseCommandQueue {
    //     return BaseService.commandQueue;
    // }

    public getService<T extends IBaseService>(classType: ServiceType): T {
        return this.serviceLocator.getServiceByClass(classType) as T;
    }
    public getServiceByName(name: string): IBaseService {
        return this.serviceLocator.getServiceByName(name);
    }

    public getServiceByPrefix(prefix: number): IBaseService {
        return this.serviceLocator.getServiceByPrefix(prefix);
    }

    /**
     * 添加服务消息回调
     * @param msg 服务消息的消息数
     * @param listener 回调
     */
    public addServiceMessageCallback(msg: number, listener: IListener) {
        const prefix = Math.floor(msg / 1000);
        this.getServiceByPrefix(prefix).addListener(msg, listener);
    }

    /**
     * 删除服务消息回调
     * @param msg 服务消息的消息数
     * @param listener 回调
     */
    public removeServiceMessageCallback(msg: number, listener: IListener) {
        const prefix = Math.floor(msg / 1000);
        this.getServiceByPrefix(prefix).removeListener(msg, listener);
    }

    public attachMessageObserver(callback: (...args: any[]) => void, context: any = null) {
        this.messager.attachMessageObserver(callback, context);
    }

    public detachMessageObserver(callback: (...args: any[]) => void, context: any = null) {
        this.messager.detachMessageObserver(callback, context);
    }

    public sendMessage(msg: { cmd: string, data: any }) {
        this.messager.emitServiceMessage(msg);
    }

    /**
     * 触发服务消息回调 服务消息是服务主动推送的消息
     * @param message 消息
     */
    protected emitServiceMessage(message: { cmd: string, [key: string]: any }) {
        this.messager.emitServiceMessage(message);
    }

    private collectObjectProperties(obj: any): { [key: string]: any } {
        const detail: { [key: string]: any } = {};
        const errProps = this.getAllProperties(obj);
        console.log(errProps);
        errProps.forEach((prop) => {
            if (typeof obj[prop] === "function" || prop.startsWith("_")) {
                return;
            }
            detail[prop] = obj[prop];
        });
        return detail;
    }

    private getAllProperties(obj: any): string[] {
        const allProps: any[] = [];
        let curr = obj;
        while (curr) {
            const props = Object.getOwnPropertyNames(curr);
            props.forEach((prop) => {
                if (allProps.indexOf(prop) === -1) {
                    allProps.push(prop);
                }
            });
            curr = Object.getPrototypeOf(curr);
        }
        return allProps;
    }

}
